import { Container } from "./styles";
import IMG1 from '../../assets/js/funcao/image1.jpg';
import IMG2 from '../../assets/js/funcao/image2.jpg';
import { Video } from "../videos";

const Funcao = () => {
    const texto1 = "( ) => { }"

    return(
        <Container>
            <h2>Funces</h2>

            <h3>O que  uma funo?</h3>
            <p>Uma funo  um procedimento de JavaScript em que um conjunto de instrues que executa uma tarefa/ao ou calcula um valor de qualquer tipo.</p>

            <p>Abaixo temos duas das formas mais utilizadas de criar funes.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Na primeira criamos uma function e damos um nome a ela e damos a ordem que ela executar. Aps ela estamos chamando ela com o exemplo1(), ento assim que o documento for renderizado ela ser ativa.</p>

            <p>Na segunda temos uma constante recebendo uma arrow function, que chamamos de funo annima, particularmente acho essa a forma mais utilizada.</p>

            <p>Deixando claro que se a const no receber uma funo annima ( {texto1} ) ela no funciona como funo.</p>

            <h3>Funo sendo ativada</h3>

            <p>Assim como j foi explicado, apenas criar uma funo no  o suficiente para que ela funcione, temos que fazer o chamado por ela para que ela seja ativada e execute e ordem que est armazenada nela. Veja na imagem abaixo um exemplo para entender melhor.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Nesse exemplo temos uma arrow function que tem armazenado um if que se for confirmado ir executar o console.log. Aps isso temos a chamada com o exemplo3(), ento assim que o documento for renderizado a funo ser ativa.</p>

            <p>Para absorver melhor o contedo tente criar suas funes e brincar misturando com if else e switch.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' />

        </Container>
    );
}

export default Funcao;