import { Container } from "./styles";
import { Video } from "../videos";

const Array = () => {
    return(
        <Container>
            <h2>Array</h2>

            <h3>O que  array?</h3>
            <p>Array  uma forma de armazenar dados de uma forma que voc consiga ter acesso aos dados quando necessrio atravs de uma identificao.</p>

            <p>Um array pode ser criado j com dados em seu contedo ou pode ser criado vazio e voc adicionar os dados quando for necessrio.</p>

            <p>Vamos ver um exemplo de um array com trs elementos para voc ver como  um array.</p>

            <p>let num = [ 'one', 'two', 'three' ];</p>

            <p>Aqui temos uma varivel que est recebendo um array com trs valores. Para ter acesso aos valores do array temos que entender que a contagem na programao se inicia do "0", ento o primeiro valor que temos no array est na posio 0, e para ter acesso a esse valor e ver ele no console do navegador vamos dar um console.log(num[0]), depois experimente o console.log(num[1]), e console.log(num[2]) para ver os outros valores tambm.</p>

            <p>Ento assim fica a numerao de casas do nosso array:</p>

            <div className="function">
                <p><span>let num = [</span><br />
                      <span className="spanSpace">'one' (identificamos com [0]),</span><br />
                      <span className="spanSpace">'two' (identificamos com [1]),</span><br />
                      <span className="spanSpace">'three' (identificamos com [2])</span><br />
                ]</p>
            </div>

            <p>Ns podemos armazenar vrios tipos de dados em um array, como strings, numbers, booleans, objetos e at mesmo outro array. Veja abaixo.</p>

            <div className="function">
                <p>let num = [ 'one', 'two', 'three' ];</p>
                <p>let dados = [ 'zero', num ];</p>
                <p>ou</p>
                <p>let dados = [ 'zero', [ 'one', 'two', 'three' ] ];</p>
            </div>

            <p>Lembra que para acessar o primeiro item de um array usamos o [0]? Agora para acessar o 'one' dentro de "let dados = [ 'zero', [ 'one', 'two', 'three' ] ]" vamos usar dados[1][0], faa isso com um console.log(dados[1][0]) e veja o valor no seu console do navegador.</p>

            <p>Aproveita e faz alguns testes, tenta criar outros arrays e acessar os valores deles. Te vejo na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Array;