import { Container } from "./styles";
import { Video } from "../videos";

const LoopWhile = () => {
    const abrir = "{";
    const fechar = "}";
    const menorque = "<";
    const usuarioNome = "${usuario.nome}";
    const usuarioIdade = "${idade}";

    return(
        <Container>
            <h2>Loop While</h2>

            <p>O loop while  parecido com o loop for, na maioria das vezes voc pode escolher qual dos dois usar, mas vamos entender como o while funciona.</p>

            <p>Diferente do for, no while criamos a varivel fora, e o parmetro informa que enquanto o requisito no for satisfeito a ordem ser executada, veja no nosso exemplo abaixo que adicionamos "idade++", isse  para que o valor da varivel v aumentando assim como a ordem do terceiro parmetro do loop for, e caso voc no adicione "idade++" o loop ser executado infinitamente at que seu navegador trave ou voc consiga fech-lo, ento no esquea de adicionar "idade++".</p>

            <div className="function">
                <p>let idade = 0;</p>

                <p>
                    <span>while (idade {menorque}= 10) {abrir}</span><br />
                    <span className="spanSpace">console.log(`A idade  {usuarioIdade} anos.`);</span><br />
                    <span className="spanSpace">idade++;</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>O loop while acima tem o mesmo efeito do loop for abaixo, faa uma comparao dos dois.</p>    

            <div className="function">
                <p>
                    <span>for (let idade = 0; idade {menorque}= 10; idade++) {abrir}</span><br />
                    <span className="spanSpace">console.log(`A idade  {usuarioIdade} anos.`);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>        

            <p>Agora que voc conhece os loops eu te desafio a fazer alguns exemplos de loop for e depois recri-los com o loop while. Te espero na prxima aula.</p>


            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default LoopWhile;