import { Container } from "./styles";
import { Link } from "react-router-dom";
import { Video } from "../videos";
import IMG1 from '../../assets/projeto1img3.jpg';
import IMG2 from '../../assets/projeto1img4.jpg';
import IMG3 from '../../assets/projeto1img5.jpg';

const Projeto1 = () => {
    return(
        <Container>
            <h2>Projeto 1</h2>

            <h3>Iniciando estilizao</h3>
            <p>Dando continuidade ao Projeto 1, vamos iniciar com algumas configuraes gerais para deixar as margens: 0 e paddings: 0, com isso fica mais fcil de posicionar os itens exatamente onde queremos, o box-sizing: border-box e a font-family: Arial.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>No "body" adicionamos um max-width: 1100px, isso significa que mesmo em telas maiores o contedo do body no ultrapassa os 1100px. Para centralizar o body foi adicionada a margin-inline: auto, dessa forma mesmo que a largura da tela aumentar o preenchimento da margem lateral vai aumentando tambm e assim o body continua centralizado. Para deixar as letras um pouco mais separadas foi utilizado o letter-spacing: 1px, ento teremos 1px de espao entre as letras.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>No resto do contedo temos a utilizao do display: flex e justify-content para posicionar os itens no local desejado.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>Vamos dar uma ateno especial para o uso do "@media screen" na linha 83 do cdigo css, com esse comando ns podemos definir configuraes que sero adicionadas a partir de uma determinada quantidade de pixels com min-width ou at uma quantidade de pixels com o max-width. Normalmente fazemos o uso dessa media query para diferenciar a estilizao do contedo mobile para o contedo desktop. Lembrando que cada dispositivo tem seu tamanho de tela em px.</p>

            <p>Com esse pequeno projeto vimos como utilizar algumas tags importantes e estilizaes que utilizamos no dia a dia enquanto programamos.</p>

            <p>Agora que voc j viu assuntos sobre HTML e CSS tente fazer um de nossos <Link to={"/dpv/exercicios"}>exerccios</Link>, botar a mo no cdigo sempre  a melhor forma de aprender.</p>

            <h3>Veja o vdeo para entender melhor a criao do cdigo</h3>
            <Video 
                link="https://www.youtube.com/embed/h4ds7bZNDAA"
            />

        </Container>
    );
}

export default Projeto1;