import { Container } from "./styles";
import { Link } from "react-router-dom";
import IMG1 from '../../assets/html/projeto2/projeto2img1.jpg';
import IMG2 from '../../assets/html/projeto2/projeto2img2.jpg';
import IMG3 from '../../assets/html/projeto2/projeto2img1-1.jpg';
import IMG4 from '../../assets/html/projeto2/projeto2img1-2.jpg';

const Projeto2 = () => {
    return(
        <Container>
            <h2>Projeto 2</h2>

            <h3>Pesquisa de satisfao</h3>
            <p>Nesse projeto iremos utilizar HTML, CSS e Javascript, por isso ser preciso ter o conhecimento bsico sobre essas tecnologias para entender a lgica do projeto. E para uma prtica ainda mais legal, vamos fazer um projeto de duas pginas. Let's go!</p>

            <p>Na primeira imagem temos o contedo HTML da primeira pgina (index.html), com uma estrutura de criao bsica criada atravs do atalho "!".</p>

            <p>Dentro da tag "body" iniciamos com uma div que contm uma imagem dentro. Por esse projeto ser inspirado nos desafios do site frontend mentor as imagens foram do site deles, mas voc pode baixar suas prprias imagens para uso. No esquea da class estrela na div, pois com ela iremos fazer a estilizao.</p>

            <p>Vamos ter uma ateno especial na div de class btns, nela temos nossos botes de notas. Cada boto tem uma class e um id, no esquea de coloc-los, pois cada um tem sua funo. Cada boto est acionando a funo clicou() e enviando um parmetro relacionado ao texto, nesse caso a nota.</p>

            <p>A prxima div est com outro boto, esse  o que chama a funo confirmar que permite ir para a prxima pgina aps a votao.</p>

            <p>No esquea de adicionar a tag "script" relacionando seu documento HTML com o Javascript.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo HTML" />
            </div>

            <p>O prximo passo  fazer a pgina 2 (index2.html), essa ser a pgina de agradecimento por o cliente ter feito a votao.</p>

            <p>Mais uma vez, no se esquea de adicionar a class e id das tags, pois a estilizao depende deles.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de cdigo HTML" />
            </div>

            <p>Veja abaixo como ficaram as duas pginas.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de cdigo HTML" />
            </div>  

            <div className="images">
                <img src={IMG4} alt="imagem com exemplo de cdigo HTML" />
            </div>        

            <p>Para continuar esse projeto v para a <Link to='/site-devparatodos/css'>pgina CSS</Link>. Te vejo l!</p>

        </Container>
    );
}

export default Projeto2;