import BackButton from "../../backButton";
import { Container } from "./style";

const Paralax = () => {
    return(
        <Container>
            <div className="main">
                <h1>Exerccio com efeito parallax</h1>
                <h3>Qualidades do caf</h3>
                <section>
                    <p>
                        O caf, alm de ser delicioso, faz muito bem para a sade. O caf pode ser um aliado na luta contra doenas como cncer, Alzheimer, depresso, estresse e diabetes. Alm disso, essa deliciosa bebida ajuda na digesto e at rejuvenesce as clulas do corpo.
                    </p>
                </section>
                <section className="grid" id="img1">
                    <p className="texto">
                        Esses so alguns dos benefcios que um caf especial pode representar para sua sade quando consumido diariamente. Num passado no muito distante, a cincia se ocupou em entender e esclarecer alguns mitos que envolvia o gro. Essa busca resultou em diversas concluses que quase ningum imaginava.
                    </p>
                    <p className="texto">
                        O Dr. Darcy Lima, autor do livro 101 Razes para tomar caf, foi o pioneiro nos estudos dedicados  nossa bebida favorita. Na sequencia, outros pesquisadores e mdicos como o Dr. Bruno Mahler Mioto do Instituto do Corao de So Paulo (InCor) dedicaram anos para compreender a complexidade e a real relevncia do ingrediente para a sade.
                    </p>
                </section>
                <section>
                    <p>
                        Foi descoberto, por exemplo, que o caf conta com potssio, zinco, ferro, magnsio e diversos minerais em pequenas quantidades alm da cafena. Ainda possui aminocidos, protenas, lipdeos, alm de acares e polissacardeos. Segundo o site da ABIC, o principal segredo  que o caf possui uma enorme quantidade de polifenis antioxidantes, chamados cidos clorognicos (que tambm ajuda na perda de peso).
                    </p>
                </section>
                <section className="grid" id="img2">
                    <p className="texto">
                        Segundo o site Sade Dica, a incidncia do cncer de mama, cancro da faringe e oral, prstata, fgado e esofgico pode ser evitada com a ingesto de pelo menos uma xcara de caf por dia. Por ser rico em antioxidantes listados acima, ajuda a evitar a degradao e alterao das clulas. Prevenindo, assim, o surgimento de mutaes que podem resultar em tumores.
                    </p>
                    <p className="texto">
                        Em um estudo feito simultaneamente nos Estados Unidos, Portugal e Brasil ficou comprovado que o caf e sua cafena ajudam a melhorar o humor, aliviar o estresse e ainda garante mais desempenho no tempo de reao, memria e raciocnio.
                    </p>
                </section>
                <section>
                    <p>
                        A pesquisa (leia na ntegra em ingls aqui) foi realizada com ratos e nesse experimento, os animais passaram por situaes estressantes como banho gelado e inclinao na gaiola. Durante trs semanas, metade deles tomou gua com cafena e a outra metade tomou gua normal. Depois desse tempo, notou-se que consumiram apenas gua normal apresentavam nveis alterados de estresse perceptveis quimicamente no crebro e no comportamento. Os animais cafeinados no apresentaram alteraes.
                    </p>
                </section>
            </div>
            <BackButton />
        </Container>
    );
}

export default Paralax;