import { Container } from "./styles";
import { Video } from "../videos";

const Objeto = () => {
    const abrir = "{";
    const fechar = "}";

    return(
        <Container>
            <h2>Objeto</h2>

            <h3>O que  objeto?</h3>
            <p> um grupo de informaes em que veremos propriedades e caractersticas. Assim como um array, um objeto nos ajuda a armazenar informaes ou nos fornece essas informaes.</p>

            <p>Vamos ver agora como seria um objeto com informaes sobre uma pessoa.</p>

            <div className="function">
                <p><span>let pessoa = [</span><br />
                      <span className="spanSpace">nome: 'Joo',</span><br />
                      <span className="spanSpace">idade: 55,</span><br />
                      <span className="spanSpace">profissao: 'professor'</span><br />
                      <span className="spanSpace">ativo: true</span><br />
                ]</p>
            </div>

            <p>Criamos uma varivel que recebeu o objeto com as informaes, e assim como podemos ter array dentro de array, podemos ter objeto dentro de objeto, veja o exemplo abaixo.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'Joo',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">profissao: 'professor'</span><br />
                    <span className="spanSpace">ativo: true</span><br />
                    <span className="spanSpace">filhos: {abrir}</span><br />
                    <span className="spanSpace2">filho1: 'Maria',</span><br />
                    <span className="spanSpace2">filho2: 'Jos'</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>Tambm podemos ter arrays dentro de objetos.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'Joo',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">profissao: ['professor', 'empreendedor'],</span><br />
                    <span className="spanSpace">ativo: true,</span><br />
                    <span className="spanSpace">filhos: {abrir}</span><br />
                    <span className="spanSpace2">filho1: 'Maria',</span><br />
                    <span className="spanSpace2">filho2: 'Jos'</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>Agora vamos ver como acessar as informaes desse objeto, com o "console.log(pessoa.nome)" temos acesso ao nome, para acessar o nome do primeiro filho iremos usar console.log(pessoa.filhos.filho1), e para acessar a segunda profisso que est no array usaremos console.log(pessoa.profissao[1]).</p>

            <p>Pronto, voc j conhece os objetos, tenta criar alguns exemplos com suas informaes e de pessoas que voc conhea, at porque  praticando que se aprende. At a prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Objeto;