import { Container } from "./styles";
import IMG1 from '../../assets/js/templatestring/image1.png';
import IMG2 from '../../assets/js/templatestring/image2.png';
import { Video } from "../videos";

const TemplateString = () => {
    return(
        <Container>
            <h2>String X template string</h2>

            <h3>O que  uma template string?</h3>
            <p>J sabemos que uma string  o tipo que damos aos textos, e a template string tambm  uma string, mas com algumas coisinhas a mais, veja nesse exemplo.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>No cdigo acima temos duas variveis com nome e sobrenome e uma terceira varivel recebendo uma template string, veja que colocamos o "$" e abrimos e fechamos chaves para colocarmos o valor da varivel dentro.</p>

            <h3>Outras formas de uso</h3>

            <p>Alm de apenas imprimir o valor da varivel, tambm podemos alterar na hora de us-la, veja na imagem abaixo um exemplo com nmeros.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Ao dar o comando console.log voc ver que uma varivel que tem valor 50 e  somada  5 ter uma impresso de 55. Aproveita e tenta fazer algumas operaes como exerccio de prtica.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/fJQxP07Uak0' />

        </Container>
    );
}

export default TemplateString;