import { Container } from "./styles";
import { Link } from "react-router-dom";
import IMG1 from '../../assets/projeto1img1.jpg';
import IMG2 from '../../assets/projeto1img2.jpg';
import { Video } from "../videos";

const Projeto1 = () => {
    return(
        <Container>
            <h2>Projeto 1</h2>

            <h3>Criando o projeto</h3>
            <p>Primeiro passo  criar uma a pasta que ir guardar nosso projeto, aps escolher/criar a pasta clique com o boto direito e abra a pasta com o VSCode. Caso voc tenha dificuldades em entender algum termo utilizado aqui recomendo que d uma olhada na nossa pgina de "<Link to={"/dpv/introduce"} >introduo</Link>", nela voc ensinamos o que  necessrio saber antes de iniciar a criar cdigos.</p>

            <p>Agora com o projeto criado e aberto no editor de cdigos, crie a estrutura bsica inicial para iniciar seu cdigo, lembre-se que temos o atalhos e ao digitar "!" o VSCode nos mostra opes com estruturas para iniciar-mos.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo HTML" />
            </div>

            <p>Acima temos o nosso "nav" criado com um "h2" e uma "div" com alguns links dentro. Veja que dentro dos links temos alguns "SVGs". Um "SVG"  um formato de imagem que est em cdigo puro, e nesse caso so cones do Instagram, Facebook e Youtube que baixei no site <Link to={"https://www.svgrepo.com/"}>svgrepo.com</Link>. Quando entrar no site voc pode pesquisas o cone pelo nome e escolher o que deseja utilizar e fazer o download. Aps realizar o download, coloque o arquivo no seu projeto assim como no exemplo acima. O arquivo ser um pouco extenso dependendo da image, mas no se preocupe, normalmente os cdigos dos SVGs so bem grandes.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de cdigo HTML" />
            </div>

            <p>Agora criamos o "main" e o "footer". Na tag "h1" coloque o seu nome o nome de algum personagem para seu projeto. J na tag "p" voc pode escolher digitar algo ou apenas inserir um "Lorem", quando digitamos Lorem no editor de cdigos ele insere um texto alternativo, caso voc digite Lorem100 ele ir inserir um texto alternativo com 100 palavras, e assim por diante.</p>

            <p>A imagem utilizada (Homem careca fumando charuto)  um "png" para que facilite no visual do nosso site. Voc pode inserir uma foto sua no formato que desejar.</p>

            <p>Agora que estamos com a parte do HTML inserida, vamos para a continuao no CSS. Te vejo l!</p>

            <h3>Veja o vdeo para entender melhor a criao do cdigo</h3>
            <Video 
                link="https://www.youtube.com/embed/0x3d5hrtB9M"
            />

        </Container>
    );
}

export default Projeto1;