import { Container } from "./styles";
import { Video } from "../videos";

const Comentario = () => {
    return(
        <Container>
            <h2>Comentrio no cdigo Javascript</h2>

            <h3>Entendendo o que  um comentrio</h3>
            <p>Quando estamos trabalhando em nosso projeto, seja em um documento Javascript, HTML ou at mesmo CSS, ns iremos muitas vezer precisar comentar uma parte do cdigo para verificar alguma coisa. Ao comentar um trecho de cdigo ele no ser renderizado no navegador, isso significa que apenas ser possvel ver o trecho comentado no cdigo.</p>

            <p>Para comentar uma linha de cdigo Javascript s  necessrio inserir duas barras no incio dela (//), assim toda a linha ser comentada. Caso voc insira as duas barras no meio de uma linha de cdigo, apenas o contedo aps as barras ir ser comentado (let x = 10 // esse  um comentrio)</p>

            <h3>Teclas de atalho</h3>

            <p>Para facilitar nossa vida, podemos selecionar uma ou mais linhas e utilizar as teclas de atalho "CTRL + ;" e assim a linha ou linhas selecionadas sero comentadas. Isso ajuda muito, pois em algumas ocasies queremos saber como o projeto se comporta sem algumas linhas de cdigo, ento ns as comentamos para ver o resultado.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/A_Hb7xTnfpM' />

        </Container>
    );
}

export default Comentario;