import BackButton from "../../backButton";
import { Container } from "./style";
import IMG1 from './assets/financas.jpg';
import IMG2 from './assets/saude.jpg';
import IMG3 from './assets/politica.jpg';
import IMG4 from './assets/desemprego.jpg';
import IMG5 from './assets/combustivel.jpg';
import IMG6 from './assets/esportes.jpg';

const Grid = () => {
    return(
        <Container>
            <header>
                <h1>Notcias do dia</h1>
                <h3>Tudo de primeira mo para voc</h3>
            </header>
            <div className="quadros">
                <div className="quadro">
                    <img src={IMG1} />
                    <div>
                        <h3>Mercado financeiro</h3>
                        <p>Aps crise mundial, Brasil se destaca com baixa inflao...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG2} />
                    <div>
                        <h3>Sade pblica</h3>
                        <p>Em discurso durante evento anual da sade, ministro da ...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG3} />
                    <div>
                        <h3>Notcias polticas</h3>
                        <p>Tribunal Superior Eleitoral estima que 20% dos brasileiros...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG4} />
                    <div>
                        <h3>Baixa no desemprego</h3>
                        <p>Aps crise mundial, Brasil segura a menor taxa de desemprego...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG5} />
                    <div>
                        <h3>Preo do combustvel</h3>
                        <p>Baixa histrica no preo do combustvel comea a ser vista...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
                <div className="quadro">
                    <img src={IMG6} />
                    <div>
                        <h3>Notcias esportivas</h3>
                        <p>Seleo brasileira se prepara para o prximo amistoso contra...</p>
                        <div className="btn">
                            <button id="btn1">Ler agora</button>
                            <button id="btn2">Salvar</button>
                        </div>
                    </div>
                </div>
            </div>
            <BackButton />
        </Container>
    );
}

export default Grid;