import { Container } from "./styles";

import IMG1 from '../../assets/explicacss01.png';
import IMG2 from '../../assets/explicacss02.png';
import IMG3 from '../../assets/exemploCss.png';
import IMG4 from '../../assets/navegadorCss.png';

const CSS1 = () => {
    return(
        <Container>
            <h3>O que  CSS?</h3>
            <p>Agora vamos ter uma breve noo de CSS (Cascading Style Sheets). E antes de iniciar vamos esclarecer que tanto o HTML quanto o CSS no so considerados linguagens de programao, mas com a grande evoluo de configuraes do CSS j existem alguns pensamentos de comparao entre algumas funes do CSS e do JavaScript. Como mensionado antes, uma forma mais fcil de entender o CSS  saber que ele  apenas para estilizao do documento, com ele podemos adicionar cores, alterar o tamanho do contedo inserido, escolher a ordem e a posio de cada item, entre muitas outras coisas. A forma mais organizada de usar o CSS  criando um arquivo .css separado do arquivo .htlm (essas so terminaes utilizadas no editor de cdigo para dizer qual o contedo do arquivo). No final da tag head inserimos uma tag link para ligar o arquivo .html em uso ao arquivo .css que estamos criando para estilizar o documento. E assim, se no houver um arquivo .css j criado, o editor dar a opo de criar um e j o deixa conectado. Dentro das nossas tag HTML podemos inserir algumas tags CSS, como por exemplo a class e o id, utilizamos essas tags para chamar a configurao da tag HTML no arquivo .css e alter-la. J no arquivo .css podemos inserir, por exemplo, um background-color para mudar a cor de fundo, um color para alterar a cor da letra caso seja um pargrafo, um height para definir a altura, width para definir a largura, font-size/font-weight/font-family para alteraes nas letras. Esses so apenas alguns dos muitos comandos que iremos aprender.</p>

            <h3>Algumas tags CSS</h3>
            <ul>
                <li>Font-size - configura o tamanho da fonte.</li>

                <li>Font-weight - configura o peso da fonte, normalmente uma fonte de letra tem vrios tamanhos e pesos, temos o bold que  o negrito e o bolder que  um negrito mais forte dizendo de uma forma mais leiga.</li>

                <li>Font-family - configura a fonte que ser utilizada, dependendo de como a utilizamos podemos configurar a fonte de uma palavra ou de todo o site.</li>

                <li>Height - configura a altura do item selecionado, caso se defina a altura de uma div por exemplo de 150px ela aparecer com essa configurao na tela.</li>

                <li>Width - configura a largura do item selecionado, caso se defina a largura de uma div por exemplo de 150px ela aparecer com essa configurao na tela.</li>

                <li>Cursor - define a forma que o cursor ir aparecer quando ele estiver em cima do item, por exemplo o "cursor: pointer" que deixar o cursor em forma de "mo" o que normalmente indica que este item  clicvel.</li>

                <li>Padding - o padding  um preenchimento que inserimos ao item selecionado, podemos usar o padding para inserir em todos os lados, padding block para inserir na parte de cima e de baixo, padding inline para inserir nos lados esquedo e direito, e tambm podemos usar em um lado especfio como padding-top, padding-bottom, padding-left, padding-right.</li>

                <li>Border - utilizada para inserir uma borda no item selecionado, com ele conseguimos definir a largura da borda, a cor, se ela ser contnua ou pontilhada, entre outras coisas.</li>

                <li>Border-radius - com ele podemos definir uma forma arredondada na borda e tambm podemos escolher qual ponto da borda ser arredondado. Por exemplo se colocar "border-radius: 1px 2px 3px 4px" o lado superior esquerdo ter um arredondamento de 1px, o superior direito de 2px, o inferior direito de 3px e o inferior esquerdo de 4px.</li>

                <li>Text-decoration - com essa tag conseguimos realizar algumas configuraes nos textos, como por exemplo o "text-decoration: underline" que deixa o texto selecionado sublinhado.</li>
            </ul>

            <h3>Quem criou o CSS?</h3>
            <p>Em 1994, Hkon Wium Lie convidou Bert Bos para criarem o projeto do CSS (Cascading Style Sheets). Em 1995 ele apresentaram o projeto e a W3C (World Wide Web Consortium) ficou interessado pelo plano e croiu uma equipe liderada por Hkon Wium Lie e Bert Bos para continuar o desenvolvimento do projeto que veio a ser lanado em 1996 sua primeira verso.</p>

            <h3>Para qu serve o CSS e como utilizar?</h3>
            <p>O CSS no  uma linguagem de programao mas sim uma forma de estilizao do documento que  apresentado na tela. Quando criamos um documento em HTML ele fica praticamente um bloco abaixo do outro, sem nenhum tipo de estilizao. Antes era possvel realizar algumas estilizaes com o HTML, mas isso mudou e vem ficando cada vez mais fora de uso, pois o HTML  um documento que inserimos contedo com sentido e no apenas por estilo, a parte de estilizao ns deixamos com o CSS. Para utilizar o CSS ns criamos um arquivo ".css" e ligamos ele com um link ao documento ".html". Tambm podemos fazer a estilizao inline, colocando cada cdigo CSS dentro das tags HTML, mas essa no  uma tcnica bem vista pelas boas prticas. Tambm podemos abrir uma tag style no "head" no HTML e inserir o CSS. Mas tambm no  recomendado pelas boas prticas da programao.</p>
            
            <h3>Dando continuidade, vamos ver de fato a estilizao do CSS funcionando.</h3>

            <p>Antes de iniciar, vamos ver uma coisa interessante que faz parte do HTML, mas que devemos saber ao usar o CSS.

            Veja abaixo que quando inserimos uma tag no HTML ela oculpa toda uma caixa que vai do lado esquerdo da tela ao lado direito, e se apenas inserir-mos o contedo um aps o outro eles iro ficar um abaixo do outro e todos eles ocupam toda uma caixa. Voc ver com o passar do tempo que o entendimento sobre isso far sentido quando estiver colocando itens um ao lado do outro, j que para fazer isso teremos que alterar a configurao da caixa em que a tag HTML est inserida.

            A primeira imagem retrada o que foi explicado anteriormente na tela do navegador. E a segunda imagem  o que normalmente vemos quando abrimos o inspecionar do navegador para conseguirmos enxergar melhor nosso documento no navegador. Para abrir o inspecionar  s clicar com o boto direito do mouse e clicar em inspecionar.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo css" />
                <img src={IMG2} alt="Imagem do navegador mostrando resultado do cdigo css" />
            </div>

            <h3>Explicando a utilizao do "ID" e da "CLASS"</h3>

            <p>Primeiro vamos entender que estamos criando um arquivo ".css" e temos que vincular ele ao arquivo ".html", temos que conectar eles atravs de um link. Ento vamos criar o arquivo "style.css" e criar o link rel="stylesheet" href="exemploCss.css" para conseguirmos realizar a estilizao do documento.

            Agora com o arquivo ".css" criado vamos entender o conceito de "id" e "class", todos os dois so mtodos que podemos utilizar para realizar a estilizao. Vamos entender a diferena entre os dois.

            ID - inserindo um "id" na nossa tag "html" ns conseguimos chamar essa tag no documento ".css" atravs de um "#". Por exemplo, pegamos uma tag p e colocamos um id= texto, agora vamos no arquivo ".css" e chamamos essa tag como #texto{} veja que abrimos e fechamos o "{}",  dentro deles que iremos inserir as configuraes css para estilizao. Mas o "ID" s poder ser utilizado em uma nica tag HTML, ento no conseguimos inserir o mesmo ID em 3 tags por exemplo.

            CLASS - j com o "class" ns identificamos a tag HTML assim class= textos e no documento ".css" chamamos ela como ".textos", veja que para a "class" utilizilamos o "." na parte CSS. E diferente do ID, com a CLASS voc poder utilizar essa class em quantas tags HTML quiser.</p>

            <h3>Agora vamos comear o nosso primeiro exemplo de cdigo em CSS</h3>

            <p>J com nosso arquivo ".css" criado e feito o link dele no arquivo ".html" vamo comear nossas configuraes. A primeira ser utilizando o " * " que aqui significa uma configurao global do arquivo, iremos inserir:</p>

            <ul>
                <li>padding: 0; para que inicialmente todo o padding (preenchimento) das tags fiquem zeradas.</li>
                <li>border: 0; para que as bordas fiquem zeradas.</li>
                <li>margin: 0; para que as margens fiquem zeradas.</li>
                <li>box-sizing: border-box; aqui estamos dizendo que independente do que tivez na tag, o tamanho dela ser contado do final das bordas.</li>
            </ul>

            <p>Agora para nosso body vamos usar:</p>

            <ul>
                <li>width: 100vw; para que o body fique com toda a largura da nossa tela, esse "vw" significa viewport width = largura da tela em termos mais leigos.</li>
                <li>height: 100vh; aqui tambm temos uma configurao de viewport, mas essa  viewport height, ou seja, altura da tela.</li>
                <li>display: flex; o display flex nos permite manusear os itens dentro da nossa tag, por exemplo, se quisermos posicionar os itens desse body em colunas devemos inserir um display flex e um flex-direction collumn, mas como queremos que eles fiquem um abaixo do outros iremos no iremos dizer que quermos em linhas pois no precisa, mas para deixar em linhas utilizamos o flex-direction row. O display flex  bem mais complexo que isso, ento recomendamos uma leitura mais aprofundada sobre ele.</li>
                <li>align-items: center; com esse  como se passassemos uma linha ao centro tag e dissessemos que tudo ficar alinhado de acordo com essa linha, nem ir exceder para baixo, nem para cima, isso porque estamos com nosso display em forma de linha, ento essa linha imaginria ser horizontal.</li>
                <li>justify-content: center; com esse deixaremos os itens dentro do body ao centro dele, e como estamos utilizando display em forma de linhas, o justify-content trata o contedo como coluna e ir igualar ao meio da tela o contedo.</li>
                <li>background-color: rgb(235, 232, 232); com essa configurao estamos inserindo uma cor de fundo, e nesse caso  uma cor quase branca, ela tem um leve escurecimento para ajudar na visualizao do usurio, pois cores muito claras ou vvidas podem acabar nos ofuscando durante a vizualizao e causando sensao de desconforto.</li>
            </ul>

            <p>Para o nosso main vamos iserir:</p>

            <ul>
                <li>width: 350px; aqui estamos inserindo uma largura, veja que ela est em pxels.</li>
                <li>padding: 20px; com esse preenchimento ns evitamos que o contedo dentro do main fique prximo demais das bordas ou at mesmo que encoste, assim com o padding teremos algo mais organizado.</li>
                <li>background-color: rgb(84, 187, 120); essa cor de fundo  uma cor esverdeada para destacar o main do body.</li>
                <li>display: flex; o display flex como explicado anteriormente nos ajuda a escolher o direcionamento do contedo, e aqui iremos usar o direcionamento de coluna como mostra a configurao a seguir.</li>
                <li>flex-direction: column;</li>
                <li>justify-content: center;</li>
                <li>gap: 20px; esse gap  praticamente um disanciamento entre os itens.</li>
                <li>box-shadow: 0px 0px 6px .5px; aqui estamos inserindo uma sombra ao redor para dar uma sensao de que o main est um pouco elevado do body e por isso fica o sombreamento ao redor, devido a elevao.</li>
            </ul>

            <p>Para o nosso ttulo h1 e para nossas tags p:</p>

            <ul>
                <li>text-align: center; faz com que os textos fiquem centralizados.</li>
            </ul>

            <p>Agora vamos utilizar um id, e lembrando que no css referenciamos ele com uma #.</p>
            
            <ul>
                <li>
                #caixa {
                    <p>padding: 20px;</p>
                }
                </li>
            </ul>

            <p>Agora temos o uso de uma class, lembrando que referenciamos ela no css com um " . ".</p>

            <ul>
                <li>
                .function {
                    <><p>display: block; com esse block, diferentemente do flex, ele deixar o contedo fixo onde quer que ele esteja.</p><p>margin: auto; aqui estamos dizendo que queremos que seja inserida uma margem automica para deixar nosso contedo centralizado, ento o navegador ir deixar a margem malevel para ele conseguir centralizar.</p><p>border: 1px solid; com essa configurao estamos inserindo uma borda ao redor do contedo, podemos escolher a largura, com esse slido estamos dizendo que ela ser contnua e sem detalhes, tambm poderamos ter inserido uma cor, mas no foi necessrio.</p><p>width: 5px;</p><p>height: 5px;</p><p>background-color: black;</p><p>cursor: pointer; aqui estamos dizendo o seguinte, quando o cursor estiver por cima do contedo  para aparecer a mozinha como se indicasse alguma coisa diferente naquele local.</p></>
                }
                </li>
            </ul>

            <p>Essa prxima agora  uma configurao de algo que ficar escondido na tela e saparecer quando o cursor estiver sobre o ponto preto na tela.</p>

            <ul>
                    
                <li>
                        .none {
                            <><p>display: none;</p><p>margin: auto;</p><p>width: 110px;</p><p>background-color: aliceblue;</p><p>font-size: 20px;</p><p>box-shadow: 0px 0px 3px 0.5px;</p><p>border-radius: 20px; o border radius nos permite adicionar uma caracterstica arredondada ao contedo.</p><p>cursor: pointer;</p></>
                        }
                </li>
            </ul>

            <h3>Agora veja como fica no editor de cdigo e o resultado no navegador</h3>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de cdigo css" />
                <img src={IMG4} alt="Imagem do navegador mostrando resultado do cdigo css" />
            </div>

        </Container>
    );
}

export default CSS1;