import { Container } from "./styles";
import { Video } from "../videos";

const LoopFor = () => {
    const abrir = "{";
    const fechar = "}";
    const maiorque = ">";
    const menorque = "<";

    return(
        <Container>
            <h2>Loop For</h2>

            <h3>O que  um loop?</h3>
            <p>Um loop  quando voc quer executar um cdigo mais de uma vez. Na programao  muito comum fazer o uso dos loops para obter algum resultado, independente que qual linguagem seja.</p>

            <p>Vamos ver um exemplo de um loop for simples como um contador que ir executar um console.log 10 vezes.</p>

            <div className="function">
                <p>
                    <span>for(let n = 1; n {menorque} 11; n++) {abrir}</span><br />
                    <span className="spanSpace">console.log('usurio ' + n);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>No exemplo acima estamos utilizando o loop for que diz o seguinte, criou a varivel "n" e enquanto o valor de "n" for menor que 10 ser somado 1 e executada funo do console.log. Ento ir aparecer 10 mensagens (usurio 0 at usurio 9).</p>            

            <p>Veja que a contagem foi a partir de "usurio 0", caso voc queira fazer a contagem iniciar de "usurio 1" ser necessrio que n comece com 1 (n = 1). Fazendo isso ns teremos nosso loop executado nove vezes, e para que ele volte a ser executado dez vezes indo at o "usurio 10" voc pode adicionar 11 ao segundo parmetro (n {menorque} 11).</p>

            <p>Na prxima aula iremos usar um loop em um array, mas antes disso voc pode tentar criar alguns loops para aprender mais como eles funcionam. At a prxima.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default LoopFor;