import { Container } from "./styles";
import { Video } from "../videos";

const Objeto2 = () => {
    const abrir = "{";
    const fechar = "}";

    return(
        <Container>
            <h2>Alterando informaes de objetos</h2>

            <p>J aprendemos que objetos so um grupo de informaes, agora vamos aprender a inserir ou alterar essas informaes. Let's go.</p>

            <p>Vamos continuar com o mesmo exemplo da outra aula e vamos alterar o nome para Miguel atravs do seguinte comando: pessoa.nome = 'Miguel';</p>

            <p>Agora vamos adicionar 20 anos  idade com o comando: pessoa.idade += 20;</p>

            <p>E tambm adicionar uma nova profisso com: pessoa.profissao.push('pai');</p>

            <p>Use o "console.log(pessoa)" e veja as alteraes o console do navegador.</p>

            <p>Agora que j sabemos manipular algumas informaes dos objetos, vamos ver um modelo de objeto mais complexo e aprender a acessar as informaes dele.</p>

            <div className="function">
                <p>
                    <span>let pessoa = {abrir}</span><br />
                    <span className="spanSpace">nome: 'Joo',</span><br />
                    <span className="spanSpace">idade: 55,</span><br />
                    <span className="spanSpace">profissao: [</span><br />
                    <span className="spanSpace2">{abrir} periodo: 'manh', cargo: 'professor' {fechar},</span><br />
                    <span className="spanSpace2">{abrir} periodo: 'tarde', cargo: 'empreendedor' {fechar}</span><br />
                    <span className="spanSpace">],</span><br />
                    <span className="spanSpace">ativo: true,</span><br />
                    <span className="spanSpace">filhos: {abrir}</span><br />
                    <span className="spanSpace2">filho1: 'Maria',</span><br />
                    <span className="spanSpace2">filho2: 'Jos'</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    {fechar}
                </p>
            </div>

            <p>Veja que na profisso adicionamos dois objetos dentro de um array e para acessar a informao do cargo da primeira profisso usaremos o comando: console.log(pessoa.profissao[0].cargo);</p>

            <p>Por as profisses estarem dentro de um array devemos utilizar as chaves ([]) para acessar os dados.</p>

            <p>Legal que voc tenha chegado at aqui, aproveita e faz um exemplo diferente e tenta acessar as informaes dele. Te vejo na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Objeto2;