import { Container } from "./styles";
import { Video } from "../videos";

const EventoTeclado2 = () => {
    const texto1 = 'const soltar = (event) => {';
    const texto2 = "console.log(event.code);";
    const fechar = '}';
    const texto3 = "const input = document.querySelector('input');";
    const texto4 = "input.addEventListener('keyup', soltar);";
    const texto5 = "console.log(event.key);";
    const texto6 = "console.log(event.shiftKey);";

    return(
        <Container>
            <h2>Eventos de teclado 2</h2>

            <p>Agora vamos ver mais alguns eventos de teclado, o "event.code", "event.key", "event.shiftKey", "event.altKey" e o "event.ctrltKey".</p>

            <p>Com o "event.code" ns conseguimos ver qual tecla foi apertada, se voc apertar a tecla "n" o valor apresentado ser "KeyN", veja no exemplo a seguir em que criamos uma funo que est pegando o evento como parmetro e dele estamos tirando o "code". Essa forma de verificar a tecla consegue diferenciar o shift esquedo do direito, j o "event.key" no consegue.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto2}</span><br />
                    <span>{fechar}</span><br />
                    <span>{texto3}</span><br />
                    <span>{texto4}</span><br />
                </p>
            </div>

            <p>J com o "event.key" conseguimos ver o valor da tecla apertada, se voc apertar o "n" o valor apresentado ser "n". Lembrando que se houver duas teclas iguais como os "shift", o "event.key" no ir diferenciar as duas.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto5}</span><br />
                    <span>{fechar}</span><br />
                    <span>{texto3}</span><br />
                    <span>{texto4}</span><br />
                </p>
            </div>

            <p>Com esse prximo, o "event.shiftKey", iremos conseguir ver se a tecla "shift" estava acionada quando alguma tecla foi apertada. O resultado ser um boolean, podendo ser true ou false.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto6}</span><br />
                    <span>{fechar}</span><br />
                    <span>{texto3}</span><br />
                    <span>{texto4}</span><br />
                </p>
            </div>

            <p>Da mesma forma que fizemos com o "event.shiftKey", podemos fazer com o "event.altKey" e o "event.ctrltKey". Esses eventos so mais utilizados em jogos ou em casos muitos especficos, mas  bom voc saber como eles funcionam, pois cada coisa criada na programao tem sua serventia.</p>

            <p>Aproveita e usa esses ltimos dois para criar seus exemplos de reviso. Te vejo na prxima aula.</p>
            
            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default EventoTeclado2;