import { Container } from "./styles";
import IMG1 from '../../assets/js/condicionalternario/image1.jpg';
import { Video } from "../videos";

const CondicionalTernario = () => {
    const texto1 = "{execute isso}";
    const texto2 = "{execute esse outro}"
    const texto3 = ">=";

    return(
        <Container>
            <h2>Condicional Ternrio</h2>

            <h3>O que seria um condicional ternrio?</h3>
            <p>Seria um if else feito com menos cdigo, enquanto o if else precisa de algumas linhas, um condicional ternrio  feito em apenas uma linha. Vamos ver uma condicional feita com if else e com condicional ternrio para entender a diferena.</p>

            <p>If else: if(idade == true) {texto1} else {texto2}</p>

            <p>Ternrio: let idade = correto ? "Questo correta" : "Questo errada"</p>

            <p>No ternrio estamos dizendo o seguinte, o valor que a varivel idade vai receber depende de correto, se correto existir e for verdadeiro a varivel recebe o primeiro valor (Questo correta), caso contrrio recebe o segundo valor(questo errada).</p>

            <p>Alm de fazer com valores exatos podemos utilizar sinais condicionais como por exemplo: let nascimento = dataNascimento {texto3} 2010 ? "true" : "false".</p>

            <p>Veja na imagem abaixo alguns exemplos e tente recri-los para ver o resultado. Aproveita e cria alguns exemplos como exerccio de fixao.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/NPPl8ko0MGY' />

        </Container>
    );
}

export default CondicionalTernario;