import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoAtributos = () => {
    const texto1 = 'function clicar () {';
    const texto2 = "const input = document.querySelector('input');";
    const texto3 = "let place = input.getAttribute('placeholder');";
    const texto4 = "window.alert(place);";
    const fechar = '}';
    const se = "if (input.hasAttribute('placeholder')) {";
    const senao = '} else {';
    const texto5 = "window.alert('No tem placeholder');";
    const texto6 = "input.setAttribute('placeholder', 'Placeholder adicionado');"

    return(
        <Container>
            <h2>Manipulando Atributos</h2>

            <h3>GetAttribute</h3>

            <p>Com o "getAttribute" conseguimos pegar o atributo do nosso elemento, no exemplo abaixo estamos pegando o atributo de um input e colocando o contedo do placeholder dele em um alert.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto2}</span><br />
                    <span className="spanSpace">{texto3}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <h3>HasAttribute</h3>

            <p>Usamos o "hasAttribute" para saber se o elemento selecionado tem aquele atributo que queremos. Para isso podemos usar um if else para fazer essa verificao. No exemplo abaixo estamos dizendo o seguinte, se houver o atributo pegue o contedo dele e execute um alert com o contedo, caso no tenha, execute um alert dizendo que no tem placeholder.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto2}</span><br />
                    <span className="spanSpace">{se}</span><br />
                    <span className="spanSpace2">{texto3}</span><br />
                    <span className="spanSpace2">{texto4}</span><br />
                    <span className="spanSpace">{senao}</span><br />
                    <span className="spanSpace2">{texto5}</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <h3>SetAttribute</h3>

            <p>Tambm podemos adicionar um atributo com o "setAttribute", vamos fazer um exemplo em que a funo ser acionada ao clique, e se o "input" tiver "placeholder" o valor dele ser mostrado em um alert, caso no tenha, ser adicionado um placeholder "Placeholder adicionado".</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                    <span className="spanSpace">{texto2}</span><br />
                    <span className="spanSpace">{se}</span><br />
                    <span className="spanSpace2">{texto3}</span><br />
                    <span className="spanSpace2">{texto4}</span><br />
                    <span className="spanSpace">{senao}</span><br />
                    <span className="spanSpace2">{texto6}</span><br />
                    <span className="spanSpace">{fechar}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoAtributos;