import { Container } from "./styles";
import { Video } from "../videos";

const EventoClique = () => {
    const texto1 = '<button onclick="clicar()">clicar</button>';
    const texto2 = 'const clicar = () => {';
    const texto3 = 'console.log("Boto clicado!");';
    const texto4 = '}';

    return(
        <Container>
            <h2>Evento de clique</h2>

            <h3>O que  isso?</h3>
            <p>Dentre vrios eventos que acontecem, temos o evento de clique que usamos para acionar uma funo. Normalmente usado em botes, o evento de clique  utilizado no documento HTML ao adicionarmos a tag do boto da seguinte forma: {texto1}. Assim no s estamos criando a tag, mas tambm dizendo que quando o boto for clicado a funo "clicar" ser ativada.</p>

            <p>Abaixo temos um exemplo da funo "clicar" feita no documento ".js" em que a ao executada pela funo ser um console.log.</p>

            <div className="function">
                <p>
                    <span>{texto2}</span><br />
                    <span className="spanSpace">{texto3}</span><br />
                    <span>{texto4}</span>
                </p>
            </div>

            <p>Existem vrios outros eventos que acontecem quando estamos navegando em uma pgina, como por exemplo o "onclick" pode ser comparado  uma juno entre o "pressdown" e o "pressup", mas com certeza o evento mais utilizado pelo Javasccript  o "onclick".</p>

            <p>Para se familiarizar mais com o "onclick" voc pode tentar criar mais botes que acionam funes, afinal  com a prtica que aprendemos cada vez mais. Te vejo na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default EventoClique;