import { Container } from "./styles";

import IMG1 from '../../assets/vscode.jpg';
import IMG2 from '../../assets/vscode2.jpg';
import IMG3 from '../../assets/vscode3.jpg';
import { Link } from "react-router-dom";

const IntroduceContent = () => {
    return(
        <Container>
            <h3>Explicaes iniciais</h3>
            <p>No mundo da programao existem vrios tipos de tecnologia que usamos para desenvolver cdigos de projetos que so frontend, backend, fullstack, mobile, entre outros. Aqui iremos iniciar com tecnologias frontend bsicas para deixar uma boa base de entendimento para quem quer entrar no mundo da programao.</p>

            <p>De incio iremos nos habilitar para fazer o uso de cdigos ".html", ".css" e ."js(javascript)"</p>

            <h3>Coisas que precisamos</h3>
            <p>1 - editor de cdigos (Visual Studio Code que  gratuito e um dos mais conhecido atualmente). Ele no  a nica forma de editar cdigos HTML, at com o bloco de notas podemos escrever um cdigo e abrir ele no navegador, basta ele estar em arquivo .html. A vantagem de um editor de cdigos  que eles nos ajudam na hora de criar o cdigo com auto preenchimento em algumas partes e tambm destacam os tipos de informaes com cores diferentes para facilitar a identificao tanto de algo que j procuramos quanto de erros aparentes em nosso cdigo.</p>

            <div className="images">
                <img src={IMG1} alt="imagem do editor Visual Studio Code" />
            </div>

            <p>2 - para adquirir o VSCode precisamos entrar no site dele e baixar para instalao. Ao entrar em "<Link target={"_blank"} to="https://code.visualstudio.com/" >https://code.visualstudio.com/</Link>" automaticamente o sistema que voc usa ser identificado (Windows, Mac ou Linux) e ser disponibilizado um boto para download e voc ir baixar a verso Stable Build (que normalmente j est selecionada). Assim que baixar  s fazer a instalao e depois de concluda  s abrir e us-lo.</p>

            <div className="images">
                <img src={IMG2} alt="imagem do site Visual Studio Code" />
            </div>

            <p>3 - Agora vamos criar nosso primeiro arquivo para poder editar os cdigos atravs do VSCode. Para manter a organizao  recomendvel que voc crie uma pasta em um local especfico para ir guardando seus projetos criados. Como exemplo de explicao iremos criar essa pasta na rea de trabalho clicando com o boto direito do mouse e depois em Novo e iremos dar o nome a essa pasta de projetos. Depois entre na pasta projetos e crie outra pasta com o nome " projeto-1 ", depois de criada voc ir clicar com o boto direito na pasta projeto-1 e clicar na opo de abrir com Code. Pronto, agora j temos nosso projeto criado e disponvel para edio do arquivo HTML, CSS e Javascript.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de cdigo HTML" />
            </div>

            <p>4 - Essa  uma das formas que existem de criar um projeto para uma pgina Web. Mais a frente iremos ver outras formas de criar projetos que sero para utilizar com outras tecnologias, e para isso iremos conhecer o uso do NODEJS que ir nos auxiliar muito no processo.</p>

        </Container>
    );
}

export default IntroduceContent;