import { Container } from "./styles";
import IMG1 from '../../assets/projetojs1img1.jpg';
import IMG2 from '../../assets/projetojs1img2.jpg';
import { Video } from "../videos";

const Variaveis = () => {
    return(
        <Container>
            <h2>Variveis e constantes (var, let, const)</h2>

            <h3>O que  uma varivel/constante?</h3>
            <p>Assim como na matemtica temos o uso das letras como variveis que server para calcular os valores que elas significam, na programao elas so criadas e um valor  definido para elas.</p>

            <p>Ao criar uma varivel x e definir-mos um valor 10 a ela (var x = 10) ento essa varivel vale pelo number 10. E por ser uma "var" ns podemos redefinir seu valor posteriormente dando outro valor a ela como o number 50 por exemplo (x = 50).</p>

            <p>Existem as variveis "let" e "var" e tambm a constante "const". Para um primeiro contato voc precisa apenas entender que as variveis podem ter seus valores alterados e a constante no.</p>

            <h3>Qual a diferena entre Let e Var?</h3>

            <p>Normalmente quando usamos uma "var" ns a utilizamos fora de funes e quando utilizamos a "let"  para um valor de uso especfico em uma funo ou algo parecido.</p>

            <h3>Tipos de dados recebidos em variveis e constantes</h3>

            <p>Os tipos so: number, string, boolean, undefined, NaN, null, array e object. No fim desse texto deixamos um vdeo explicando detalhadamente.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/RG9PdKO1hDw' />

        </Container>
    );
}

export default Variaveis;