import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoDom = () => {
    const texto1 = '<h1 id="titulo">Ttulo principal</h1>';
    const texto2 = '<button onclick="clicar()">clicar</button>';
    const texto3 = 'function clicar () {';
    const texto4 = "document.getElementById('titulo').innerHTML = 'Novo ttulo'";
    const texto5 = "}";
    const texto6 = "document.getElementById('titulo').innerText = 'Novo ttulo';";

    return(
        <Container>
            <h2>Manipulando o DOM</h2>

            <p>A essa altura do campeonato voc j aprendeu a criar seu documento HTML e tambm alguns passos do Javascript, agora vamos aprender a manipular o contedo que temos no HTML, o que chamamos de manipulao do DOM.</p>

            <p>Uma das formas que podemos fazer isso  com o "innerHTML", com esse comando estamos alterando o contedo interno do elemento.</p>

            <p>Vamos fazer a manipulao de um ttulo do html ({texto1}) atravs do clique em um boto ({texto2}) com a funo abaixo que troca de 'Ttulo principal' para 'Novo ttulo'.</p>

            <div className="function">
                <p>
                    <span>{texto3}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span>{texto5}</span>
                </p>
            </div>

            <p>J com o "innerText" alteramos o texto de algum elemento que o contenha, pode ser um h1, h2, h3, h4, h5, h6, p, li, ou outros que tenham texto. Mesmo sendo parecidos, o innerHTML  mais utilizado que  innerText, mas voc precisa conhecer os dois, pois na programao cada coisa tem sua utilizade.</p>

            <div className="function">
                <p>
                    <span>{texto3}</span><br />
                    <span className="spanSpace">{texto6}</span><br />
                    <span>{texto5}</span>
                </p>
            </div>

            <p>Agora que voc conhece o innerHTML e o innerText aproveita e faz alguns exemplos, nos vemos na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoDom;