import { Container } from "./styles";
import { Video } from "../videos";

const FuncaoParametro = () => {
    const texto1 = "const somar = (parametro1, parametro2) => {";
    const texto2 = "var resultado = parametro1 + parametro2;";
    const texto3 = "console.log(`O resultado da soma  de: ` + resultado);";
    const texto4 = "}";
    const texto5 = "somar(10, 55);";
    const texto6 = "somar(10);";

    return(
        <Container>
            <h2>Parmetros de uma funo</h2>

            <p>Agora vamos aprender a enviar parmetros (dados externos) para serem utilizados em uma funo.</p>

            <p>Mas antes vamos entender o que  um parmetro e para isso vamos criar uma funo chamada somar que recebe dois parametros para podermos fazer uma ao de soma, esses valores so recebidos dentro dos parnteses da funo.</p>

            <p>A fica a dvida, de onde vm esse valores? Fique tranquilo, pois eles vm de onde estamos chamando (dando a ordem de agir) a funo, vamos deixar exemplos aqui abaixo para voc entender melhor.</p>

            <p>Nesse primeiro exemplo temos uma funo chamada somar que precisa de dois parmetros para poder agir, parametro1 e parametro2, e os dados que eles iro receber esto sendo enviados logo depois com "somar(10, 55)". Aps isso, a varivel resultado est recebendo a soma dos dois parmetros e o resultado  impresso no navegador com o console.log.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span> <br />
                        <span className="spanSpace">{texto2}</span> <br />
                        <span className="spanSpace">{texto3}</span> <br />
                    <span>{texto4}</span><br /><br />
                    <span>{texto5}</span>
                </p>
                <p>Impresso no console: O resultado da soma  de: 65</p>
            </div>

            <p>Caso a funo esteja esperando dois parmetros e s receber um o resultado ser um erro, nesse caso se voc retirar um dos dois acima a impresso ser "NaN".</p>

            <div className="function">
                <p>
                    <span>{texto1}</span> <br />
                        <span className="spanSpace">{texto2}</span> <br />
                        <span className="spanSpace">{texto3}</span> <br />
                    <span>{texto4}</span><br /><br />
                    <span>{texto6}</span>
                </p>
                <p>Impresso no console: O resultado da soma  de: NaN</p>
            </div>

            <p>Para melhor aprendizado, recomendo que voc faa algumas funes e tente ir modificando os dados, afinal quanto mais treinamos mais aprendemos. Bons estudos e te vejo na prxima.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default FuncaoParametro;