import { useState } from "react";
import { Container } from "./styles";
import IMG1 from '../../assets/exemploJs.png';

const JS1 = () => {
    const [show, setShow] = useState(false);

    const open = () => {
        let box = document.querySelector('.box');

        if(show) {
            box?.classList.add('.none');
            box?.classList.add('back');
            setShow(false);
            
        } else {
            box?.classList.remove('.none');
            box?.classList.remove('back');
            setShow(true);
        }
    }

    return(
        <Container>
            <h3>O que  Javascript?</h3>
            <p>O JavaScript  uma das linguagens de programao mais conhecidas e antes de continuar precisamos entender que JavaScript e Java so duas linguagens diferentes (vale a pena dar uma estudada). Voc j deve ter acessado uma pgina Web e recebido um sinal de alerta com alguma informao ou at mesmo foi alertado quando esqueceu de digitar uma informao em algum campo ou digitou de forma incorreta, ento voc j conheceu alguma funo desenvolvida pelo JavaScript.</p>

            <h3>Algumas coisas que conseguimos fazer com o JavaScript.</h3>

            <ul>
                <li>Console.log - com esse comando conseguimos fazer alguns testes de forma que no aparea na tela para o usurio e sim no console do navegador que acessamos nas ferramentas, essa  uma tima forma de verificar se estamos conseguindo fazer nosso cdigo funcionar.</li>

                <li>Document.querySelector - com esse comando conseguimos acessar o primeiro item do documento HTML com aquela caracterstica para fazermos alguma ao com ele. Tambm temos o querySelectorAll que seleciona todos os itens com aquela caracterstica.</li>

                <li>Const - com a constante podemos definir um valor para ela, e ela  muito utilizada no em JS, quando definimos que a "const one = 1" ento o valor de one ser o nmero 1, ento da prxima vez que formos precisar usar o 1 no precisamos digitar ele e sim a constante one. Com esse exemplo parece no fazer sentido, mas quando estamos fazendo o cdigo isso pode nos ajudar muito.</li>

                <li>Let - tem uma funcionalidade parecida com a const, mas normalmente utilizamos quando precisamos de uma constante apenas dentro de uma funo.</li>

                <li>Var -  uma varivel com funces parecidas com a constante, mas por conseguir alterar seu valor de forma mais fcil ela no  muito recomendada pelas boas prticas. Por exemplo, quando colocamos "var caixa = carro" e depois colocamos "var caixa = moto", o valor  alterado de carro para moto.</li>
            </ul>

            <h3>Quem criou o Javascript?</h3>
            <p>Com o nome inicial de LiveScript, a linguagem JavaScript foi criada por volta de 1995 por Brendan Eich a pedido da empresa Netscape. Mas a SUN Microsystems tambm ficou interessada no JS e comeou a trabalhar no desenvolvimento da linguagem.</p>

            <p>Por a SUN Microsystems j manter a linguagem Java, esse fato fez com que a LiveScript tivesse seu nome alterado para JavaScript. Mas temos que lembrar que Java e JavaScript so linguagens tanto diferentes em seus conceito quanto em seus usos.</p>

            <h3>Agora vamos comear o nosso primeiro exemplo de cdigo em CSS</h3>
            <p>Com o JavaScript vamos ver primeiro as imagens e depois veremos as explicaes de cada passo feito.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Primeiro vamos entender o que so esses "let", "const" e "var". O LET e o VAR so variveis, conseguimos dar a ele valores de tags, por exemplo, para que no seja preciso estar digitando o processo pra acessar essa tag toda vez que for preciso. Com as variveis podemos alterar seus valores, caso voc d um valor 'um' para uma varivel e 10 linhas depois d um valor 'dois' para a mesma let, era ficar com o valor 'dois' dentro dela. Com a CONST  diferente, o valor definido para uma constante no pode ser alterado aps definido. Por as variveis serem to maleveis, o seu uso deve ser muito restrito ou at mesmo evitado, pois alm no ser considerada uma boa prtica ela pode ter seu valor alterado de forma indevida e acabar estragando seu cdigo.</p>

            <p>A forma mais aconselhvel de utilizar a varivel LET  para uma funo especfica, e ela podem ser criadas dentro da funo e fora dela. Quando ela  criada fora da funo ela pode ser chamada em mais de um local (escopo geral), mas se ela for criada dentro da funo ela s ter valor dentro dessa referida funo (escopo restrito).</p>

            <p>A CONST tem um uso parecido com a LET e a VAR, mas como explicado antes, ela no ter seu valor alterado depois que for definido, e a CONST  a mais aconselhvel para uso em seu cdigo, isso evitar que voc acabe gerando bugs no seu cdigo.</p>

            <p>Agora vamos aprender sobre nossas funes, como mostra na imagem, nossas funes so formadas da seguinte maneira: "function"  pra informar que temos a funo, "mostrar()"  o nome da nossa funo e dentro dos "{}" colocamos as aes que sero executadas quando a funo for acionada.</p>

            <p>No nosso exemplo, as nossas funes iro executar aes para alterar a cor de fundo do main atravs das constantes e variveis utilizadas e tambm iremos trocar os displays das classes none e function para que quando uma estiver visvel a outra no aparecer.</p>

            <div className="main">
                <div className="box">
                    <h1>
                        Meu primeiro site
                    </h1>
                    <p>
                        Esse  meu primeiro exemplo de site e eu estou aprendendo com o Dev Para Todos.
                    </p>
                    <p>
                        Reza a lenda que para aprender de verdade como programar temos que utilizar a frase "Ol mundo!", caso voc no a use em seu primeiro projeto a magia da programao no lhe aceitar e voc no ir conseguir criar nenhum projeto.
                    </p>
                    <div>
                        <p>
                            Ento para seguir a tradio passe o cursor do mouse por cima do quadrado e veja a mgica acontecer.
                        </p>
                        <div onClick={open}>
                            {show &&
                                <div id="caixa" >
                                    <div className="dot"  />
                                </div>
                            }
                            {!show &&
                                <div id="caixa" className="none" >
                                    <p>
                                        Ol mundo!
                                    </p>
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </div>

            {/* <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo HTML" />
                <img src={IMG2} alt="Imagem do navegador mostrando resultado do cdigo HTML" />
            </div> */}

        </Container>
    );
}

export default JS1;