import { Container } from "./styles";

import IMG1 from '../../assets/react1.jpg';
import IMG2 from '../../assets/react2.jpg';
import IMG3 from '../../assets/react3.jpg';
import IMG4 from '../../assets/react4.jpg';
import IMG5 from '../../assets/react5.jpg';
import { Link } from "react-router-dom";

const ReactJS1 = () => {
    return(
        <Container>
            <h3>O que  ReactJS?</h3>

            <p>O React  uma biblioteca feita em linguagem Javascript, criada pela empresa do Facebook para ajudar a reduzir o processamento e melhorar o desenpenho, mesmo em mquinas de pouca memria de processamento.</p>

            <h3>Como o React funciona?</h3>

            <p>O React funciona com o Virtual DOM. Praticamente o React deixa o contedo pr-carregado no Virtual DOM, quando em algum momento fazemos uma mudana de contedo com click, rolagem ou alguma requisio, o contedo que ser alterado  o do Virtual DOM, e aps realizar essa alterao ele compara o Vitual DOM com o DOM de fato e executa a alterao apenas do contedo que est diferente entre eles.</p>

            <p>Ento o que vemos carregado em nossa tela  o que chamamos de SPA (Single Page Application). Quando o documento  executado ele carrega o HTML com apenas uma div, e  o contedo dessa div que ser manipulado pelo React com a troca pr-carregada entre o Vitual DOM e o DOM da nossa mquina.</p>

            <h3>O que so os componentes do React?</h3>

            <p>O React  baseado em uma estrutura de componentes, isso significa que o projeto final  resultado de vrios componentes que contem uma parte de cdigo criados de forma separada e interligados atravs de exportao e importao.</p>

            <h3>Qual a vantegem do uso de componentes no React?</h3>

            <p>Por cada parte do projeto ser criada em seu prprio componente a manuteno se torna mais fcil, j que basta identificar o componente e fazer sua alterao de forma separada sem alterar os dados dos outros componentes.</p>

            <p>Alm dos componentes facilitarem na manunteno do projeto, tambm  possivel fazer a reutilizao dos componentes, isso significa que se houver algum trecho de cdigo que ser repetido em vrios locais do projeto s ser preciso criar um componente para ele e utiliz-lo quando for necessrio.</p>

            <h3>Como podemos ter um exemplo da reutilizao de componentes?</h3>

            <p>Podemos usar os botes como exemplo e cri-los em componentes separados, j que normalmente so trechos de cdigo que se repetem em um projeto e utiliz-los quando for preciso, dessa forma iremos, alm de manter um projeto mais organizado, deixar um modelo padronizado para todos os botes do projeto.</p>

            <h3>O que precisamos ter para utilizar o ReactJS?</h3>

            <p>Para trabalhar com o ReactJS precisamos ter o NodeJS instalado em nosso computador. Para saber se j temos ele instalado devemos abrir o nosso prompt de comando (caso esteja no windows, se estiver em um MacBook  terminal), e para isso iremos dar o comando "npm -v" no nosso prompt de comando e ir aparecer a verso que voc tem instalada, e caso no tenha  s ir no site do NodeJS e fazer o download para instalao.</p>

            <p>Para fazer a instalao voc deve ir em "<Link to='https://nodejs.org' target={"_blank"}>https://nodejs.org</Link>", depois ir em downloads. Se sua mquina for de 32-bit voc escolhe ele, caso seja 64-bit voc escolhe ele.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem do site do NodeJS" />
            </div>

            <p>Depois de instalado voc ir novamente no prompt de comando e digitar o comando "node -v" para confirmar se foi instalado e ver a verso.</p>

            <p>Quando instalamos o NodeJS ele vem tambm com o NPM (Node Package Manager) que  uma ferramenta que iremos utilizar muito, e para ver a verso do NPM  s dar o comando "npm --version".</p>

            <div className="images">
                <img src={IMG2} alt="Imagem do prompt de comando mostrando as verses do NodeJS e do NPM" />
            </div>

            <p>Aps essas verificaes ns j podemos comear a criar nosso projetos ReactJS.</p>

            <h3>Como criamos um projeto React?</h3>

            <p>Iremos escolher a pasta que iremos criar nosso projeto. Em nosso exemplo vamos criar da forma recomendada, ou seja, criar uma pasta (a nossa ser projetos) nova no disco local para armazenar todos os seus projetos de forma organizada.</p>

            <p>Abrimos o nosso prompt de comando e ele mostrar que estamos em C:\Users\usuario, agora vamos voltar duas pastas com o comando "cd.." e depois "cd.." novamente e depois "cd projetos", agora estamos dentro da nossa pasta projetos e iremos iniciar a criao.</p>

            <p>Vamos criar utilizando o Create React App que  a tcnica oficial de criao de projetos React. Vamos dar o comando "npx create-react-app react-primeiro --template typescript", isso significa que estamos criando nosso projeto com o mtodo CRA, ele ser chamado de react-primeiro e ele j ser criado habilitado para o uso de Typescript. Esse processo de criao pode levar alguns minutos, principalmente se for a primeira vez que voc estiver criando um projeto.</p>

            <p>Agora que temos nosso projeto criado, podemos fechar o prompt, ir at nossa pasta projetos, clicar com o boto direito na pasta do nosso projeto e abrir ele com o VSCode. Pronto, agora vamos entender a estrutura do nosso projeto, pois ele ser criado com vrios arquivos e temos que saber os que iremos utilizar de incio.</p>

            <div className="images">
                <img src={IMG3} alt="Imagem do VSCode mostrando o projeto criado" />
            </div>

            <p>Veja que ao abrir o projeto no VSCode temos algumas pastas criadas, e  na pasta "src" que iremos focar. Nela temos o "app.tsx" que  o arquivo que ser renderizado atravs do arquivo "index.tsx" e mostrado na tela.</p>

            <h3>O uso do JSX no React</h3>

            <p>JSX significa "JavaScript Xml", que  um cdigo Javascript que utiliza sintaxe HTML para escrita de cdigo. O JSX  uma forma de criar o cdigo mais fcil, veja abaixo um exemplo feito utilizando o JSX e outro sem usar.</p>

            <p>No primeiro (sem o JSX) utilizamos os trs parmetros necessrios para criao dentro de um React.createElement( 1, 2, 3), onde o primeiro  a tag, o segundo a referncia dela e o terceiro o contedo.</p>

            <div className="images">
                <img src={IMG4} />
            </div>

            <p>J no segundo criamos como se fosse HTML dentro de cdigo Javascript e o compilador Babel faz o trabalho de levar os dados da forma que ele necessita para a impresso na tela.</p>

            <div className="images">
                <img src={IMG5} />
            </div>

            <h3>Como o JSX  interpretado?</h3>

            <p>Para que o JSX seja interpretado pelos navegadores  necessrio que seja transformado em cdigo Javascript comum atravs de alguma ferramenta, como a Babel por exemplo, que  um compilador Javascript feito por um conjunto de ferramentas, e uma de suas funes  converter JSX para ser lido pelos navegadores como Javascript.</p>

            <h3>O uso de "ClasseName" para identificar objetos</h3>

            <p>O termo "class" j  reservado para o Javascript, ento para no haver conflitos, o termo "ClassName" foi aderido como atributo nos objetos JSX.</p>

            <h3>Como rodar o projeto?</h3>

            <p>Ao abrir o terminal no editor de cdigos iremos digitar o comando "npm run start" que  o comando que vem como padro para inicializar o projeto em nossa mquina. Com isso ser aberta uma aba no navegador com um indereo local da mquina "http://localhost:3000". Para confirmar essa informao voc pode abrir o arquivo "package.json" e descer at onde ficam os "scripts" e ver quais os comandos podem ser utilizados.</p>

        </Container>
    );
}

export default ReactJS1;