import { Link } from "react-router-dom";

const GithubContent = () => {
    return(
        <Container>
            <h3>O que  Git?</h3>

            <p>o git serve para que voc v versionando seu cdigo, isso significa que voc tem a opo de a cada alterao feita em seu cdigo voc v salvando a verso alterada e se voc criar uma parte nova do cdigo que apresente algum erro que voc no consiga identificar pra resolver, voc poder voltar para a verso salva anteriormente antes de ser feito o cdigo que apresenta erro. Praticamente  uma forma de garantir que voc no perca todo o cdigo por causa de um pequeno erro.</p>

            <p>Outra forma de ajuda que o GIT oferece com o versionamento  a possibilidade de mais de uma pessoa trabalhar no mesmo projeto e toda vez que cada uma salvar sua alterao, por ela estar versionada, o arquivo pode ser retornado para a data de antes da apresentao do erro e sem atrapalhar os colegas. Tudo sempre com um histrico apresentando as alteraes.</p>

            <p>Caso duas pessoas alterem a mesma parte do cdigo ser apresentado um conflito, isso ajuda para que um no apague ou sobrescreva alguma coisa que outra pessoa criou.</p>

            <h3>Como instalar o GIT?</h3>

            <p>Se direcione ao site <Link to='https://git-scm.com/downloads' target="_blank" >https://git-scm.com/downloads</Link> e escolher a vero para seu sistema operacional, tendo a opo de Windows, MacOS e Linux.</p>

            <p>Baixe o instalador e faa a instalao na sua mquina.</p>

            <p>Aps instalado reinicie o seu prompt de comando e d o comando "git --version" para ver a verso instalada.</p>

            <h3>Configurando o GIT.</h3>

            <p>Agora vamos configurar o GIT com nossos dados para poder ficar registrado toda ver que fizermos algo no GIT, como uma alterao por exemplo.</p>

            <p>No prompt vamos dar o seguinte comando para definir seu nome: git config --global user.name "seu nome aqui"</p>

            <p>O seguinte comando para seu email: git config --global user.email "seu email aqui"</p>

            <p>O seguinte comando para informar qual editor de cdigo voc est usando: git config --global core.editor nome-do-editor-de-cdigo</p>

            <p>Agora vamos aprender a ver quais as informaes cadastradas: git config --list</p>

            {/* <div className="images">
                <img src={IMG1} alt="Imagem do site do NodeJS" />
            </div> */}

            {/* <div className="images">
                <img src={IMG2} alt="Imagem do prompt de comando mostrando as verses do NodeJS e do NPM" />
            </div> */}
            
            {/* <div className="images">
                <img src={IMG3} alt="Imagem do VSCode mostrando o projeto criado" />
            </div> */}

            {/* <div className="images">
                <img src={IMG4} />
            </div> */}

            {/* <div className="images">
                <img src={IMG5} />
            </div> */}

        </Container>
    );
}

export default GithubContent;