import { Container } from "./styles";
import { Video } from "../videos";

const Array2 = () => {
    return(
        <Container>
            <h2>Operaes com arrays - length, push, pop, shift</h2>

            <h3>O que podemos fazer com os arrays?</h3>
            <p>Lembra que falamos que um array pode ser iniciado vazio e depois voc pode inserir dados? Pois , alm de inserir dados vamos te ensinar agora a ver quantos itens o array tem e a como excluir o primeiro e ltimo item do array. Let's go.</p>

            <p>Abaixo temos um array com quatro valores, e para ter acesso a quantidade no console do navegador vamos utilizar o comando console.log(paises.length), o length  o comando para saber quantos itens voc tem dentro do array, mesmo que ele seja enorme voc poder ter acesso a essa informao com o length().</p>

            <p className="function">let paises = ['Brasil', 'Peru', 'Bolvia', 'Argentina'];</p>

            <p>Para inserir um valor vamos usar o paises.push('Equador'), assim iremos adicionar 'Equador'  ltima posio ([4]) do nosso array.</p>

            <p>Com o "paises.pop()" conseguimos excluir o ltimo item do nosso array, nesse caso, se seguir a ordem iremos excluir o adicionado anteriormente (Equador).</p>

            <p>J com "paises.shift()" iremos excluir o primeiro item do nosso array, nesse caso o 'Brasil'.</p>

            <p>Veja que esses mtodos agem como funes, ento sempre lembre que eles necessitam dos parnteses para funcionarem.</p>

            <div className="function">
                <p>paises.length() - diz quantos itens temos dentro do array</p>
                <p>paises.push() - insere um item ao array</p>
                <p>paises.pop() - remove o ltimo item do array</p>
                <p>paises.shift() - remove o primeiro item do array</p>
            </div>

            <p>Com o passar do tempo voc ver outras coisas legais que conseguimos fazer com arrays, mas no se preocupe se no conseguir decorar todas, o importante  entender o que est acontecendo para que quando voc precisar fazer essas aes acima voc se lembre que esses mtodos existem para lhe ajudar.</p>

            <p>Aproveita e faz alguns exemplos para aprender cada vez mais. Te vejo na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Array2;