import { Container } from "./styles";
import { Video } from "../videos";

const ElementosDom = () => {
    const texto1 = "<h1>Ttulo principal</h1>";
    const texto2 = '<p id="texto1">texto 1</p>';
    const texto3 = '<p class="texto2">texto 2</p>';

    return(
        <Container>
            <h2>Selecionando elementos do DOM</h2>

            <h3>Como fazemos isso?</h3>
            <p>document -  o documento que est sendo renderizado na tela.</p>

            <p>Agora ns vamos aprender a selecionar os elementos pelo nome da tag, pelo id e pela class.</p>

            <h3>GetElementsByTagName</h3>

            <p>Digamos que voc tenha em seu documento HTML um "{texto1}", vamos selecionar ele pela tag utilizando document.getElementsByTagName("h1"), pronto, selecionamos o h1. Para poder trabalhar com ele, seja fazendo alteraes ou adicionar contedo voc ir precisar colocar ele em uma varivel, ento pode fazer "let titulo = document.getElementsByTagName("h1")".</p>

            <h3>GetElementById</h3>

            <p>Agora vamos selecionar um elemento atravs do id que ele tenha, digamos que voc tenha o elemento "{texto2}", para selecion-lo iremos usar "document.getElementById("texto1")", agora voc o ter selecionado. E para usar uma varivel voc pode fazer como foi feito no exemplo anterior (let texto1 = document.getElementById("texto1")).</p>

            <h3>GetElementsByClassName</h3>

            <p>A prxima maneira de selecionar um elemento  pela class que voc der a ele, vamos usar o exemplo "{texto3}", e para selecion-lo iremos usar "document.getElementsByClassName", e assim voc ter selecionado o elemento atravs da class, podendo coloc-lo em uma varivel tambm.</p>

            <h3>QuerySelector</h3>

            <p>Sem dvidas o seletor mais fcil de se usar  o Query Selector, com ele conseguimos selecionar pelo id, pela class, pela tag name, e tambm conseguimos selecionar o que est dento das tags.</p>

            <p>Vamos fazer trs exemplos para voc entender melhor.</p>

            <p>Nesse primeiro estamos selecionando atravs do id, assim como j feito com o "getElementById".</p>

            <p>document.querySelector("#texto1");</p>

            <p>Nesse outro estamos selecionando atravs da class, assim como no "getElementByClassName".</p>

            <p>document.querySelector(".lista");</p>

            <p>E nesse terceiro estamos fazendo um efeito cascata em que acessamos a class lista, depois a "ul" que est dentro dela, e por fim a "li".</p>

            <p>document.querySelector(".lista ul li");</p>

            <p>Tambm podemos utilizar o "querySelectorAll" que seleciona todos os elementos com aquelas caractersticas. Se voc tiver vrios "li" para selecionar, o querySelectorAll ser ideal para isso.</p>

            <p>document.querySelectorAll("li");</p>

            <p>Logo mais voc ver como esse assunto  importante, mas at l tenta recriar os exemplos feitos aqui trocando as informaes. Te vejo na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ElementosDom;