import { Container } from "./styles";
import { Video } from "../videos";

const LoopForArray = () => {
    const abrir = "{";
    const fechar = "}";
    const menorque = "<";
    const usuarioNome = "${usuario.nome}";
    const usuarioIdade = "${usuario.idade}";

    return(
        <Container>
            <h2>Loop For em array</h2>

            <p>Uma forma de loop que usamos muito na programao  quando damos um loop em um array, veja logo a baixo alguns exemplos de como voc pode fazer.</p>

            <p>let usuarios = ['primeiro', 'segundo', 'terceiro', 'quarto', 'cinco'];</p>

            <p>Veja que nesse primeiro loop estamos utilizando usuarios.length no segundo parmetro para que a quantidade de vezes que o loop seja executado seja igual ao mesmo nmero de quantidade de elementos no array.</p>

            <div className="function">
                <p>
                    <span>for(let n = 0; n {menorque} usuarios.length; n++) {abrir}</span><br />
                    <span className="spanSpace">console.log(usuarios[n]);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>Ao ver o resultado no console, voc pode adicionar mais um elemento no array com um "usuarios.push('sexto')" e ver novamente o resultado no console.</p>            

            <p>O prximo exemplo de loop tem o mesmo resultado do primeiro, mas os parmetros so diferentes, est criando a varivel "i" que recebe a identificao de cada elemento de usurios por vez. No console.log estamos usando essa identificao para imprimir o resultado no console.log.</p>

            <div className="function">
                <p>
                    <span>for(let i in usuarios) {abrir}</span><br />
                    <span className="spanSpace">console.log(usuarios[i]);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>Os parmetros desse terceiro exemplo criamos a varivel para o loop, mas diferente dos anteriores que agiam como array ir agir como um objeto, ento para acessar a informao devemor apenar inserir a varivel no console.log.</p>

            <div className="function">
                <p>
                    <span>for(let usuario of usuarios) {abrir}</span><br />
                    <span className="spanSpace">console.log(usuario);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>
            
            <p>Para entender melhor esse terceiro exemplo vamos criar um quarto exemplo em que temos um array com trs objetos.</p>

            <div className="function">
                <p>
                    <span>let usuarios = [</span><br />
                    <span className="spanSpace">{abrir} nome: 'primeiro', idade: 10 {fechar},</span><br />
                    <span className="spanSpace">{abrir} nome: 'segundo', idade: 15 {fechar},</span><br />
                    <span className="spanSpace">{abrir} nome: 'terceiro', idade: 20 {fechar},</span><br />
                    <span>];</span>
                </p>

                <p>
                    <span>for(let usuario of usuarios) {abrir}</span><br />
                    <span className="spanSpace">console.log(`O usurio {usuarioNome} tem {usuarioIdade} anos de idade`);</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>E esses foram os loops dessa aula, espero que voc tenha entendido o contedo, caso tenha ficado alguma dvida voc pode criar seus prprios loops e ir alterando eles para ver o resultado de cada um, afinal  a prtica que nos ensina cada vez mais. Te vejo na prxima aula.</p>



            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default LoopForArray;