import { Container } from "./styles";
import IMG1 from '../../assets/html/projeto2/projeto2img6.jpg';
import { Link } from "react-router-dom";

const ProjetoJs2 = () => {
    return(
        <Container>
            <h2>Criando o Javascript do projeto 2</h2>

            <p>Bom, se voc chegou at aqui ento j passou pelo HTML e CSS do projeto 2, caso no tenha feito, recomendo que voc volte para fazer, pois precisamos do HTML e CSS desse projeto para fazer com que ele funcione como queremos.</p>

            <p>Lembrando que por estarmos iniciando nossos estudos o nosso cdigo Javascript estar bastante autoesplicativo, e lembrando tambm que cada linha de cdigo tem sua funo, ento caso seu cdigo no funcione voc deve revisar o contedo para vorrigir eventuais error. Ento let's go!</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <h3>EventListener</h3>

            <p>Na linha 1 temos o comando element.addEventListener('click', clicou), vamos entender o que significa pois esse  um comando muito utilizado no dia a dia do programador. O "addEventListener" diz praticamente o seguinte, quando houver o evento de "click" no meu projeto eu quero que voc observe a funo "clicou" para execut-la.</p>

            <h3>Function clicou(n)</h3>

            <p>Na linha 3 temos uma funo que est recebendo um parmetro, se voc voltar l na parte de criao do HTML, voc ver que informamos esse valor em todos os botes de notas (1, 2, 3, 4 e 5) dentro das funes "clicou(parmetro)", assim saberemos qual boto est sendo clicado. </p>

            <h3>Variveis (var)</h3>

            <p>Var  uma forma de usar variveis no Javascript, no  a mais recomendada, mas estamos utilizando ela aqui para voc entender que ela existe. Uma varivel pode receber um valor e depois ter seu valor alterado, nos casos das linhas 4 a 8 temos as variveis recebendo os "id" que colocamos l no HTML dos botes.</p>

            <h3>Variveis (let)</h3>

            <p>Agora vamos ver outra forma de varivel, a "let", que  uma varivel de escopo local, isso significa que criamos esse tipo de varivel dentro de uma funo para usar o valor que ela recebe apenas nessa funo. Temos uma "let" na linha 10.</p>

            <h3>GetElemenById</h3>

            <p>Essa  uma das formas que temos para refernciar uma tag HTML dentro do Javascript, dessa forma estamos buscando a tag atravs do "id" que ela tem.</p>

            <h3>QuerySelector</h3>

            <p>O querySelector  outra forma de refernciar uma tag HTML no Javascript, mas essa forma  mais avanada que o simples getElementById, pois o querySelector pode pegar a tag por "id", "class", "tag" e outras formas, mas as mais utilizadas so essas trs.</p>

            <h3>If e else if</h3>

            <p>Traduzindo para o portugus, "if"  "se", e "else if"  "seno se". Essa  uma forma que usamos muito para dizer o seguinte:</p>

            <p> se(condio) execute isso, <br />
            seno se(condio) execute isso, <br />
            seno sxecute isso.</p>

            <p>No  recomendado pelas boas prticas que se use muitos "if else" no cdigo, mas para um incio na programao temos que aprender como usar o "if" e depois nos preocuparemos com formas mais avanadas.</p>

            <h3>Window.location.href</h3>

            <p>Esse comando que estamos utilizando na funo da linha 49 est dizendo o seguinte, quando essa funo for executada voc ir mudar para a pgina que estou indicando aqui. Lembrando que podemos mudar de pgina tando com o HTML como com o Javascript, escolher qual usar vai depender o projeto que voc est criando.</p>

            <p>Chegamos ao fim desse projeto, tente fazer e entender cada passo. Lembrando que temos mais explicaes em outros projetos aqui no DevParaTodos. Aproveita e pe em prtica o que voc est aprendendo com nossos <Link to='/site-devparatodos/exercicios'>exerccios</Link>. At mais!</p>

        </Container>
    );
}

export default ProjetoJs2;