import { Container } from "./styles";
import { Video } from "../videos";

const Dom = () => {
    return(
        <Container>
            <h2>DOM</h2>

            <h3>O que significa DOM?</h3>
            <p>Document Object Model (modelo de objeto de documento).</p>

            <p> a representao de dados dos objetos que compem a estrutura e o contedo de um documento na Web. Ou seja, o DOM  que faz o contedo renderizado aparecer na tela.</p>

            <p>Para que nosso projeto rode na web ele precisa estar em html, por isso, independente da linguagem que voc usa o contedo que aparece no navegador  em html.</p>

            <p>Para entender melhor, quando voc estudou html voc aprendeu que o arquivo fica em forma de rvore, ou seja, ele tem tags nomeadas e elas tem seu grau de importncia.</p>

            <p>A partir de agora vamos aprender mais sobre o DOM e iremos aprender a manipul-lo, trocando informaes, inserindo ou excluindo.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default Dom;