import { Container } from "./styles";
import IMG1 from '../../assets/js/ifelse/image1.jpg';
import IMG2 from '../../assets/js/ifelse/image2.jpg';
import IMG3 from '../../assets/js/ifelse/image3.jpg';
import { Video } from "../videos";

const IfElse = () => {
    return(
        <Container>
            <h2>If else</h2>

            <h3>O que  o If Else?</h3>
            <p>O if else  a nossa porta de entrada para lgica na programao do Javascript. Ele  uma forma de darmos comandos em forma de cdigo para que aes sejam executadas dependendo de informaes que sejam apresentadas. Para ficar melhor de entender vamos deixar a seguir uma imagem de um exemplo do uso do if else para voc.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>No cdigo acima temos duas variveis com nome e idade. Aps elas temos nosso "if()", dentro do parntese iremos dizer a condio que nesse caso  nome ser igual a string Jos, caso seja, o cdigo entre chaves ser executado, caso no seja, o cdigo do else (se no) ser executado.</p>

            <p>Veja na imagem abaixo um exemplo em que o "if" no ser compatvel com a condio e o "else" ser executado.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <h3>Criando If dentro de If</h3>

            <p>Alm de fazer como nos exemplos anteriores, tambm podemos criar "if" dentro de "if", mas no muitos, pois se muitos forem criados pode acarretar em confuso para entender o cdigo. Veja na imagem abaixo dois exemplos, um com dois "if" e outro com trs.</p>

            <div className="images">
                <img src={IMG3} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Em um cdigo real, o if else nos ajuda muito a filtrar informaes e at mesmo liberar acesso para pessoas autorizadas e esconder informaes que no devam ser mostradas.</p>

            <h3>Ateno!</h3>

            <p> sempre bom que voc v fazendo o cdigo e testando no seu computador, assim voc ir se familiarizar cada vez mais com o que for estudando.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/v9iQIYMuQGs' />

        </Container>
    );
}

export default IfElse;