import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoClasses = () => {
    const texto1 = 'function clicar () {';
    const texto2 = "const titulo = document.querySelector('#titulo');";
    const se = "if(titulo.classList.contains('antigaClass')) {";

    const texto3 = "titulo.classList.remove('antigaClass');";
    const texto4 = "titulo.classList.add('novaClass');";

    const senao = "} else {";
    const texto5 = "titulo.classList.remove('novaClass');";
    const texto6 = "titulo.classList.add('antigaClass');";
    const fechar = '}';

    const texto7 = "titulo.classList.toggle('novaClass');";

    const texto8 = "titulo.classList.replace('antigaClass', 'novaClass');";
    const texto9 = "titulo.classList.replace('novaClass', 'antigaClass');";

    return(
        <Container>
            <h2>Manipulando classes em elementos</h2>

            <p>Agora vamos aprender a manipular as classes dos elementos, diferente da manipulao do CSS inline, aqui iremos ver como trabalhar com classes j prontas.</p>

            <p>No exemplo abaixo temos a nossa funo clicar que ao ser executada far a conts titulo receber o id titulo. Aps isso temos o nosso if dizendo o seguinte, se a classList de titulo tiver a class "antigaClass" ento remova ela e adicione a class "novaClass", caso contrrio remova "novaClass" e adicione "antigaClass".</p>

            <p>Todos os elementos do HTML tem uma classList em que ficam suas classes de estilizao, ento mesmo que voc no adicione uma estilizao todos os elementos tero suas classList.</p>

            <p>Ento o "contains" usamos para saber se o item selecionado tem aquela class, o "add" usamos para adicionar a class e o "remove" usamos para remover a class.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto2}</span><br />
                        <span className="spanSpace">{se}</span><br />
                            <span className="spanSpace2">{texto3}</span><br />
                            <span className="spanSpace2">{texto4}</span><br />
                        <span className="spanSpace">{senao}</span><br />
                            <span className="spanSpace2">{texto5}</span><br />
                            <span className="spanSpace2">{texto6}</span><br />
                        <span className="spanSpace">{fechar}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>Caso voc queira apenas variar entre adicionar e remover uma mesma class voc pode usar o "toggle", assim como no exemplo abaixo, ele funciona como um if else, se tiver a class e retira, se no tiver ele insere.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto2}</span><br />
                        <span className="spanSpace">{texto7}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>Nesse prximo exemplo estamos usando o "replace" que significa substituir, e ele pede que voc informe a class que vai ser substituda e a que ficar no lugar dela.</p>

            <div className="function">
                <p>
                    <span>{texto1}</span><br />
                        <span className="spanSpace">{texto2}</span><br />
                        <span className="spanSpace">{se}</span><br />
                            <span className="spanSpace2">{texto8}</span><br />
                        <span className="spanSpace">{senao}</span><br />
                            <span className="spanSpace2">{texto9}</span><br />
                        <span className="spanSpace">{fechar}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>Contedo interessante no  mesmo? Tenta criar uns exemplos com suas idias. At a prxima.</p>
            
            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoClasses;