import { Container } from "./styles";
import IMG1 from '../../assets/js/switch/image1.jpg';
import IMG2 from '../../assets/js/switch/image2.jpg';
import { Video } from "../videos";

const Switch = () => {
    return(
        <Container>
            <h2>Switch</h2>

            <h3>O que  um Switch</h3>
            <p>Assim como o if else funciona com condies, o switch tambm trabalha da mesma forma, mas o switch pode agir com muito mais informao que um simples if else. Para entender melhor veja abaixo uma imagem com um cdigo em if else e outra com um cdigo feito em switch e os dois tratam do mesmo resultado.</p>

            <div className="images">
                <img src={IMG1} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Nesse cdigo temos trs variveis criadas, mas apenas a primeira est ativa, veja que no switch passamos profession por um tipo de filtro e vamos criando os cases que dizem o seguinte, se profession tiver o valor de 'fiscal' execute isso aqui e pare (break), se for 'bombeiro' execute isso e pare, se for 'policial' execute isso e pare, se no for nenhuma das opes anteriores execute isso aqui e pare.</p>

            <p> importante colocar o "break" pois se no for colocado ele executar a order e continuar na prxima linha.</p>

            <p>Agora veja o cdigo com if else.</p>

            <div className="images">
                <img src={IMG2} alt="Imagem com exemplo de cdigo Javascript" />
            </div>

            <p>Em um primeiro contato parece que o if else  a melhor opo, mas acredite em mim, o switch  uma prtica muito mais bem vista que o if else quando se trata de muitas condies em uma mesma questo, e tambm facilita muito em uma manuteno futura no cdigo.</p>

            <p>Para melhor absorver o contedo tente fazer alguns exemplos voc mesmo, crie alguns switch e if else para ir praticando.</p>

            <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/-DrstaFQAAA' />

        </Container>
    );
}

export default Switch;