import { Container } from "./styles";
import IMG1 from '../../assets/projetojs1img1.jpg';
import IMG2 from '../../assets/projetojs1img2.jpg';

const Projeto1 = () => {
    return(
        <Container>
            <h2>Juntando nomes com Javascript</h2>

            <h3>Pegando dados e inserindo no DOM</h3>
            <p>Antes de iniciar esse projeto espero que voc ja tenha visto nossa introduo sobre Javascript, pois cada detalhe que vamos aprendendo pode fazer a diferena em um projeto maior.</p>

            <h3>Ateno!</h3>

            <p>Para melhor visualizao, esse projeto estar com o HTML, CSS e Javascript no mesmo documento.</p>

            <p>No projeto fazemos o uso de trs inputs, sendo 2 para digitar os nomes e o terceiro para exibir a informao. Existem vrios tipos de inputs para os mais diversificados fins. Por exemplo temos os types: button, checkbox, color, date, datetime, email, entre outros. Caso voc no conhea os inputs recomendo que d uma pesquisada, mesmo no conseguindo absorver todo o contedo de uma vez, voc j saber as formas de input e o que eles so capazes de fazer por voc.</p>

            <p>J na parte do Javascript temos a function somar que est recebendo os valores, colocando-os em variveis e mandando-os para a tela com o res.innerHTML.</p>

            <p>Existem algumas formas de acessar o HTML pelo Javascript, uma delas  o getElementById, com ele conseguimos pegar um elemento pelo "id", temos o id="txtn1" como exemplo que est recebendo o valor digitado e a varivel tn1 o recebe atravs do window.document. getElementById('txtn1').</p>

            <p>Outra forma de acessar o HTML  com o querySelector, com ele podemos pegar o contedo pelo id e pela class, diferente do getElementById, ele pode selecionar mais de uma class com querySelectorAll, no qual ir pegar todos os elementos com a class selecionada.</p>

            <p>Por ltimo temos o comando res.innerHTML que est pegando os valores recolhidos nas variveis e enviando para o DOM. Note que alm de enviar o contedo das variveis tambm conseguimos enviar textos, desde que esteja usando as aspas invertidas, dessa forma temos uma template string, que nos permite juntar texto e cdigo.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo CSS" />
            </div>
            <div className="images">
                <img src={IMG2} alt="imagem do resultado no navegador" />
            </div>

        </Container>
    );
}

export default Projeto1;