import { Container } from "./styles";
import { Video } from "../videos";

const ManipulandoCss = () => {

    const texto2 = 'const clicar = () => {';
    const texto3 = 'const titulo = document.querySelector("#titulo");';
    const texto4 = "titulo.style.color = 'red';";
    const texto5 = "titulo.style.backgroundColor = 'yellow';";
    const texto6 = "titulo.style.fontSize = '50px';";
    const fechar = '}';

    return(
        <Container>
            <h2>Manipulando CSS inline dos elementos</h2>

            <p>Essa forma de manipulao que veremos agora  para o CSS inline, ento caso voc queira alterar a estilizao de apenas um elemento esse mtodo pode ser utilizado.</p>

            <p>Primeiro vamos selecionar o elemento de id "#titulo" com o querySelector, aps isso podemos utilizar a propriedade "style" e depois o que queremos alterar ou inserir, se for para adicionar um background-color por exemplo, daremos o comando "titulo.style.backgroundColor = 'yellow'", veja que o "backgroundColor" est em estilo camelcase, onde a primeira palavra inicia minscula e as outras se iniciam com letra maiscula e no se utiliza o trao separador.</p>

            <p>Veja nosso exemplo logo abaixo, ele est alterando a cor da letra, o background-color e o font-size do nosso ttulo.</p>

            <div className="function">
                <p>
                    <span>{texto2}</span><br />
                    <span className="spanSpace">{texto3}</span><br />
                    <span className="spanSpace">{texto4}</span><br />
                    <span className="spanSpace">{texto5}</span><br />
                    <span className="spanSpace">{texto6}</span><br />
                    <span>{fechar}</span>
                </p>
            </div>

            <p>Para se familiarizar mais com o contedo voc pode tentar criar alguns exemplos, afinal  com a prtica que aprendemos cada vez mais. Te vejo na prxima aula.</p>

            {/* <h3>Assista essa aula para entender melhor</h3>

            <Video link='https://www.youtube.com/embed/E-p5NoFjFOo' /> */}

        </Container>
    );
}

export default ManipulandoCss;