import { Container } from "./styles";

import IMG1 from '../../assets/exemploHtml.png';
import IMG2 from '../../assets/navegadorHtml.png';

const HTML1 = () => {
    return(
        <Container>
            <h3>O que  HTML?</h3>
            <p>A sigla HTML significa HyperText Markup Language (Linguagem de Marcao de HiperTexto), e sua verso mais atualizada  o HTML5. Para explicar de uma forma mais fcil de entender, ns utilizamos o HTML para inserir contedo em uma pgina web e esse contedo tem um sentido. A inteno  inserir o contedo em uma organizao atravs de tags e esse contedo no aparece com uma aparncia na qual estamos acostumados no dia a dia, mas temos que lembrar que a inteno  inserir o contedo e no a estilizao, esse  o papel do CSS que ser o nosso prximo tpico. Para ficar melhor de associar, pensaremos no HTML como um corpo, o CSS como as roupas e os movimentos do corpo como o JavaScript (que tambm veremos logo mais).</p>

            <h3>Algumas tags HTML</h3>
            <p>Agora veremos algumas tags HTML, lembrando que no so todas e que no  regra ter que decorar todas de incio, com o passar do tempo e com a prtica voc ir memorizar todas as mais utilizadas nos seus projetos. Cada tag tem em sua constituio um cdigo que define qual ser a sua funo dentro do documento, por exempo a tag "p" que defique um pargrafo.</p>

            <ul>
                <li>h1 - essa  uma tag de ttulo, sendo a tag de ttulo mais forte. Em cada pgina devemos utilizar apenas uma tag h1. Em seguida temos h2, h3, h4, h5 e h6. Cada uma delas tem um grau de importncia, sendo em ordem decrescente.</li>

                <li>p -  a tag para criar pargrafos.</li>

                <li>ul - tag utilizada para listas desordenadas.</li>

                <li>ol - tag para criar listas ordenadas.</li>

                <li>li - tag que insere os elementos de uma lista. Cada li  um elemento.</li>

                <li>button - tag utilizada para inserir um boto no cdigo.</li>

                <li>img - para inserir uma imagem.</li>

                <li>video - insere vdeos ao cdigo.</li>

                <li>script - essa tag  usada para inserir JavaScript ao cdigo, podendo fazer no mesmo arquivo. Mas o mais aconselhado  criar um arquivo novo apenas para o JS.</li>

                <li>link - com essa tag podemos inserir um link de para uma pgina com os arquivos CSS, inserir um favicon e outras funes.</li>
            </ul>

            <h3>Quem criou o HTML?</h3>
            <p>Tim Berners-Lee  o nome dele, o cara que criou o nosso querido HTML. Em 1991 o HTML foi criado para facilitar o envio de documentos por instituies de pesquisa e em 1992 ele foi liberado para uso mundial na internet com a biblioteca World Wide Web que conhecemos como WWW.</p>

            <h3>Para qu serve o HTML e como utilizar?</h3>
            <p>Com o HTML podemos criar documentos de pginas web, podemos criar e inserir os contedos de uma pgina com ele. Lembrando que com ele todo contedo inserido deve ter um sentido e no apenas por simples aparncia, a parte de estilizao fica por conta do CSS por exemplo. Para criar cdigos com HTML s precisamos ter um editor de cdigo instalado em nosso computador. Tendo como exemplo o Visual Studio Code mais conhecido como VSCode. Ao realizar a instalao do mesmo ns temos as opes de criar arquivos ".html" ".css" e ".js".</p>
            
            <h3>Agora vamos comear o nosso primeiro exemplo de cdigo em HTML.</h3>

            <p>Vamos entender aos poucos como funciona a estrutura do nosso documento HTML. Veja que ele inicia dizendo que  um documento HTML, aps isso temos o idioma que o documento est escrito e aps isso temos o head.

            Veja que dentro do head temos o charset="UTF-8", que significa praticamente o seguinte: o idioma utilizado nesse documento faz uso de palavras com acentos e cedilha. Pois o idioma portugus faz uso desses itens.

            Depois vemos o title que  o ttulo que ir aparecer na guia em seu navegador. Por exemplo, aqui o ttulo  Meu primeiro site.

            Aps a tag head temos a tag body que na traduo significa corpo, e  isso que ela , o corpo do nosso documento. Dentro do body temos a tag main e dentro dela iremos inserir o nosso contedo. Caso Tivssemos um rodap iramos inserir uma tag footer logo aps o fechamento da tag main. Mas nesse exerccio no iremos utilizar um footer.

            Abrimos a tag "h1" com o intuito de dizer que esse  o ttulo central do corpo do documento, esse  o ttulo principal. Lembrando que cada pgina s se deve utilizar um ttulo "h1", mas de "h2" at "h6" podemos utilizar quantos forem necessrios.

            A tag "p" inserida significa que temos um pargrafo de texto digitado. Digamos que seria um pargrafo de texto de um livro ou de um jornal.

            A tag "div" utilizada significa em termos menos tcnicos que estamos abrindo uma caixa para inserir mais contedo dentro dela, isso ir facilitar nosso trabalho principalmente na hora em que estiver-mos estilizando nosso documento ou inserindo funes de programao com o JS.

            Agora veja como fica o documento digitado no editor de cdigo e como aparece no navegador. Lembrando que aqui estamos apresentando o HTML puro. Para ver ele estilizado continue na explicao do CSS.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo HTML" />
                <img src={IMG2} alt="Imagem do navegador mostrando resultado do cdigo HTML" />
            </div>

        </Container>
    );
}

export default HTML1;