import { Container } from "./styles";
import { Link } from "react-router-dom";
import IMG1 from '../../assets/html/projeto2/projeto2img3.jpg';
import IMG2 from '../../assets/html/projeto2/projeto2img4.jpg';
import IMG3 from '../../assets/html/projeto2/projeto2img5.jpg';
import IMG4 from '../../assets/html/projeto2/projeto2img1-3.jpg';
import IMG5 from '../../assets/html/projeto2/projeto2img1-4.jpg';

const ProjetoCss2 = () => {
    return(
        <Container>
            <h2>Projeto 2</h2>

            <h3>Iniciando estilizao</h3>

            <p>Continuando o projeto 2, agora vamos iniciar a estilizao. Como no projeto anterior, iriei destacar as configuraes mais interessantes no momento. Ento, let's go!</p>

            <p>Para deixar nosso projeto tomando conta de toda a tela, comeamos com height e width 100%, display flex, align-itens e justify-content center, assim o contedo ficar no meio da pgina mesmo que o tamanho da tela mude.</p>

            <p>Algo para se destacar  a font-family que escolhemos a Arial. Veja que ela est da seguinte forma: "Arial, Helvetica, sans-serif", isso significa que primeiro o navegador ir tentar usar a Arial, caso no consiga ele ir tentar usar a Helvetica, e se mesmo assim no conseguir ele usar qualquer fonte sans-serif. Fontes sans-serif so as fontes que no tem aqueles detalhes pontudos, j a fonte times new rome  uma fonte com serif.</p>

            <p>Nosso contedo ser uma caixa com width de 300px e height de 300px, mas como estamos adicionando padding ele ficar um pouco maior, nesse caso 60px maior em largura e altura pois o padding est sendo adicionado em todos os lados.</p>

            <div className="images">
                <img src={IMG1} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>Algo que tambm vamos destacar  a estilizao da class "btnClicked", essa class ser adicionada ao boto de nota clicado atravs do Javascript, ento no esquea desse detalhe.</p>

            <p>Veja que a class ".sub" est com display: none, isso significa que ela no fica amostra no navegador, mas logo depois temos uma class ".subDisplay", essa class ser ativada quando o cliente escolher a nota, fazendo assim com que o boto de "Enviar" fique visvel e clicvel.</p>

            <div className="images">
                <img src={IMG2} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>Tambm estamos fazendo o uso do ":hover" nas classes ".btn:hover e ..sub button:hover", isso far com que os botes tenham uma cor de destaque quando o cursor estiver sobre eles.</p>

            <div className="images">
                <img src={IMG3} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>Essa foi a nossa estilizao do projeto, veja agora as imagens de como as pginas ficam aps o CSS.</p>

            <div className="images">
                <img src={IMG4} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <div className="images">
                <img src={IMG5} alt="imagem com exemplo de cdigo CSS" />
            </div>

            <p>Agora que voc j viu o HTML e CSS do projeto continue com o <Link to='/site-devparatodos/javascript'>Javascript</Link> e veja a lgica que iremos usar para fazer os botes funcionarem. Te vejo l!</p>

        </Container>
    );
}

export default ProjetoCss2;